<template>
    <div class="put-video-box" :style="{'--color':maincolor}">
        <div class="pos-box">
            <div class="first-img" @click="upImg()">
                <div class="hover-box" v-show="fimgshow">
                    更换封面
                </div>
                <img :src="banddatalist.fimgurl" class="f-img" v-show="fimgshow">
                <p v-show="!fimgshow">点击此处上传封面</p>
                <input type="file" v-show="false" id="up-fimg" @change="changeImg()" @click="changeImg()" accept="image/jpg,image/png,image/jpeg">
            </div>
            <div class="form-box">
                <p class="tip-p">标题</p>
                <div class="title-box">
                    <p id="title-tip">*</p>
                    <input type="text" class="text" id="title-text" v-model="banddatalist.title">
                </div>
                <p class="tip-p">简介</p>
                <div class="title-box">
                    <p id="sum-tip">*</p>
                    <textarea class="sum-box" id="sum-box" v-model="banddatalist.summary"></textarea>
                </div>
                <p class="tip-p">视频文件</p>
                <div class="title-box">
                    <div class="up-button">
                        <textarea id="video-url" readonly="readonly">请选择视频文件上传</textarea>
                        <button @click="upVideo()">上传</button>
                    </div>
                    <input type="file" v-show="false" id="up-video" @click="changeVideo()" @change="changeVideo()" accept="video/mp4">
                </div>
                <div class="up-btn-posbox">
                    <button class="up-btn" @click="putVideoCheck()">发布</button>
                    <router-link to="/Home/Main" id="put-button" v-show="false"></router-link>
                </div>
            </div>
        </div>
        <div class="tip" v-show="tipshow">
            <p id="puttip-text">有为空的选项哦</p>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    name:'PutVideo',
    data () {
        return {
            fimgshow:false,
            tipshow:false,
            banddatalist:{
                fimgurl:'',
                videourl:'',
                title:'',
                summary:''
            }
        }
    },
    computed: {
        ...mapState(["maincolor","navshow"])
    },
    mounted () {
        document.getElementById('title-text').addEventListener('change',this.checkIfNull,true);
        document.getElementById('sum-box').addEventListener('change',this.checkSumNull,true);
    },
    methods: {
        upImg(){
            document.getElementById('up-fimg').click();
        },
        changeImg(){
            let img = document.getElementById('up-fimg').files[0];
            let url = window.URL.createObjectURL(img);
            this.banddatalist.fimgurl = url;
            this.fimgshow = true;
        },
        upVideo(){
            document.getElementById('up-video').click();
        },
        changeVideo(){
            let video = document.getElementById('up-video').files[0];
            let url = window.URL.createObjectURL(video);
            this.banddatalist.videourl = url;
            let text = document.getElementById('video-url');
            text.innerText = this.banddatalist.videourl;
        },
        checkIfNull(){
            if(this.banddatalist.title != ''){
                let tip = document.getElementById('title-tip');
                tip.setAttribute('style', 'display:none');
            }else if(this.banddatalist.title == ''){
                let tip = document.getElementById('title-tip');
                tip.setAttribute('style', 'display:block');
            }
        },
        checkSumNull(){
            if(this.banddatalist.summary != ''){
                let tip = document.getElementById('sum-tip');
                tip.setAttribute('style', 'display:none');
            }else if(this.banddatalist.summary == ''){
                let tip = document.getElementById('sum-tip');
                tip.setAttribute('style', 'display:block');
            }
        },
        putVideoCheck(){
            if(this.banddatalist.fimgurl != '' && this.banddatalist.videourl != '' && this.banddatalist.title != '' && this.banddatalist.summary != ''){
                this.$https.post('http://rap2api.taobao.org/app/mock/288525/AddVideo',this.banddatalist).then((res) => {
                    document.getElementById('puttip-text').innerText = '发布成功';
                    this.tipshow = res.data.success;
                    setTimeout(() => {
                        this.tipshow = false;
                        document.getElementById('put-button').click();
                        this.$store.commit('setNavShow', [true]);
                    },1000)
                })
            }else {
                document.getElementById('puttip-text').innerText = '有为空的选项哦';
                this.tipshow = true;
                setTimeout(() => {
                    this.tipshow = false;
                },1000)
            }
        }
    }
}
</script>

<style scoped>
@import '../static/css/PutVideo.css';
</style>