<template>
    <div class="care-box" :style="{'--color':maincolor}">
        <div class="care-bar" v-for="(item,index) in userpostlist" :key="index">
            <p class="care-inf" v-if="(item.time>=1)&&(item.time<=12)">{{item.time}}个月之前</p>
            <p class="care-inf" v-if="(item.time>=13)&&(item.time<=24)">一年之前</p>
            <p class="care-inf" v-if="(item.time>=25)&&(item.time<=36)">两年之前</p>
            <div class="care-userinf">
                <img :src="item.img">
                <div class="care-usertext">
                    <p class="name">{{item.name}}</p>
                    <p class="tag">{{item.tag}}</p>
                </div>
            </div>
            <router-link :to="'/Group/'+item.id" @click.native="setArticle([item.title,item.content,item.agree,item.collection,item.other,item.care,item.look,item.img,item.name,item.tag,item.put,item.recmd,item.grap,item.comment]),changeNav()">
                <h3 class="title" :style="{'--color':maincolor}">{{item.title}}</h3>
            </router-link>
            <p class="t-content">{{item.content}}</p>
            <div class="icon-bar">
                <span class="agree" @click="agreeclick(item.id,index)"><i :class="item.agreeicon?beforeagree:agree"></i></span><p>赞同{{item.agree}}</p>
                <span class="comment" @click="showcombar(item.id,index),getincom(item.comment,item.id,index)"><i :class="comment"></i></span><p>{{item.comment}}条评论</p>
                <span class="collection" @click="collectionclick(item.id,index)"><i :class="item.collectionicon?beforecollection:collection"></i></span>
            </div>
            <div class="in-speak-bar" v-if="item.showcombar">
                <h5>共{{item.comment}}条评论</h5>
                <div v-for="(cmd,cindex) in item.incomlist.view" :key="cindex">
                    <div class="user-cmd-box">
                        <div class="user-inf">
                            <div class="user-imginf">
                                <img :src="cmd.img">
                                <p class="name">{{cmd.name}}</p>
                            </div>
                            <p class="date">{{cmd.date}}</p>
                        </div>
                        <div class="comd-content">
                            <pre class="content">{{cmd.content}}</pre>
                        </div>
                        <div class="in-icon-bar">
                            <span class="agree" @click="clickinagree(cmd.id,index,cindex)"><i :class="cmd.agreeicon?beforeagree:agree"></i></span><p>{{cmd.agree}}</p>
                            <span class="comment" @click="atrecomd(cmd.name)"><i :class="comment"></i></span>
                        </div>
                    </div>
                </div>
                <div class="page-box">
                    <ul>
                        <li>共{{item.incomlist.totalPage}}页</li>
                        <li v-for="(num,liindex) in item.incomlist.totalPage" :key="liindex" :class="{'spkindexcolor':item.incomlist.indexstyle == liindex}" @click="Indexchangestyle(item.comment,item.id,index,liindex)">{{num}}</li>
                        <li @click="Nextchangestyle(item.comment,item.id,index)">下一页</li>
                    </ul>
                </div>
                <div class="in-user-comment">
                    <textarea id="in-user-input" class="in-user-input" v-model="updatecomment.content" :style="{'outline-color':maincolor}"></textarea>
                    <button class="in-user-inputbtn" @click="addcomment(item.id,index)" :style="{'background-color':maincolor}"><p>发表</p><br><p>评论</p></button>
                </div>
            </div>
        </div>
        <div class="tip-nonull" v-if="tipshow">
            <p>回复的内容不能一个字也没有哦</p>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex"
export default {
    name:'Care',
    data () {
        return {
            agree:'iconfont icon-dianzan',
            beforeagree:'iconfont icon-dianzan1',
            collection:'iconfont icon-shoucang',
            beforecollection:"iconfont icon-shoucangtianchong",
            comment:'iconfont icon-_ico_reply',
            tipshow:false,
            userpostlist:[
                
            ],
            updatecomment:{
                id:'1',
                img:this.$store.state.userlogin.tximg,
                name:this.$store.state.user.name,
                content:'',
                date:'',
                time:'',
                agree:'0',
                agreeicon:false,
            },
            sarticle:{
                title:'',
                content:'',
                agree:'',
                collection:'',
                other:'',
                care:'',
                look:'',
                img:'',
                name:'',
                tag:'',
                put:'',
                recmd:'',
                grap:'',
                comment:''
            }
        }
    },
    beforeMount() {
        this.getPosts();
    },
    mounted() {
        this.scroll(this.userpostlist);
    },
    computed: {
        ...mapState(["article","maincolor","userlogin","user","navshow"])
    },
    methods: {
        getPosts() {
            for (let i = 0; i < 10; i++) {
                this.$https.get("http://rap2api.taobao.org/app/mock/288525/userCareInformations").then((res) => {
                    this.userpostlist.push(res.data.information[i]);
                })
            }
        },
        agreeclick(id,index){
            if(this.userpostlist[index].id==id){
                this.userpostlist[index].agreeicon = !this.userpostlist[index].agreeicon;
                if(this.userpostlist[index].agreeicon==true){
                    this.userpostlist[index].agree++;
                }else{
                    this.userpostlist[index].agree--;
                }
            }
        },
        collectionclick(id,index){
            if(this.userpostlist[index].id==id){
                this.userpostlist[index].collectionicon = !this.userpostlist[index].collectionicon;
            }
        },
        scroll(userpostlist) {
            let isLoading = false;
            window.onscroll = () => {
                let bottomOfWindow = document.documentElement.offsetHeight - document.documentElement.scrollTop - window.innerHeight <= 200;
                if (bottomOfWindow && isLoading == false) {
                    isLoading = true;
                    this.$https.get("http://rap2api.taobao.org/app/mock/288525/userCareInformations").then((res) => {
                        userpostlist.push(res.data.information[0]);
                        isLoading = false;
                    })
                }
            }
        },
        showcombar(id,index){
            if(this.userpostlist[index].id == id){
                this.userpostlist[index].showcombar = !this.userpostlist[index].showcombar;
            }
        },
        clickinagree(id,index,cindex){
            if(this.userpostlist[index].incomlist.view[cindex].id == id){
                this.userpostlist[index].incomlist.view[cindex].agreeicon = !this.userpostlist[index].incomlist.view[cindex].agreeicon;
                if(this.userpostlist[index].incomlist.view[cindex].agreeicon == true){
                    this.userpostlist[index].incomlist.view[cindex].agree++;
                }
                else{
                    this.userpostlist[index].incomlist.view[cindex].agree--;
                }
            }
        },
        getincom(pageNum,id,index){
            if(this.userpostlist[index].id == id){
                this.$https.get("http://rap2api.taobao.org/app/mock/288525/spkincmd").then((res)=>{
                    this.userpostlist[index].incomlist = res.data.comlist;
                    this.userpostlist[index].incomlist.totalPage = Math.ceil(pageNum/this.userpostlist[index].incomlist.pageSize);
                    this.userpostlist[index].incomlist.cominf.length = pageNum;
                    let page = this.userpostlist[index].incomlist.currentPage;
                    let start = this.userpostlist[index].incomlist.pageSize * page;
                    let end = this.userpostlist[index].incomlist.pageSize * (page+1);
                    this.userpostlist[index].incomlist.view = this.userpostlist[index].incomlist.cominf.slice(start,end);
                });
            }
        },
        Indexchangestyle(pageNum,id,index,liindex){
            if(this.userpostlist[index].id == id){
                this.userpostlist[index].incomlist.indexstyle = liindex;
                this.userpostlist[index].incomlist.cominf.length = pageNum;
                let page = liindex;
                let start = this.userpostlist[index].incomlist.pageSize * page;
                let end = this.userpostlist[index].incomlist.pageSize * (page+1);
                this.userpostlist[index].incomlist.view = this.userpostlist[index].incomlist.cominf.slice(start,end);
            }
        },
        Nextchangestyle(pageNum,id,index){
            if(this.userpostlist[index].id == id){
                if(this.userpostlist[index].incomlist.indexstyle<(this.userpostlist[index].incomlist.totalPage - 1)){
                    this.userpostlist[index].incomlist.indexstyle++;
                    this.userpostlist[index].incomlist.cominf.length = pageNum;
                    let page = this.userpostlist[index].incomlist.currentPage;
                    if(page+1>this.userpostlist[index].incomlist.totalPage){
                        return false;
                    }else{
                        page = page + 1;
                        this.userpostlist[index].incomlist.currentPage = page;
                    }
                    let start = this.userpostlist[index].incomlist.pageSize * page;
                    let end = this.userpostlist[index].incomlist.pageSize * (page+1);
                    this.userpostlist[index].incomlist.view = this.userpostlist[index].incomlist.cominf.slice(start,end);
                }
            }
        },
        addcomment(id,index){
            if(this.userpostlist[index].id == id){
                let input = document.getElementById("in-user-input").value;
                let str = input.replace(/(^\s*)|(\s*$)/g, "");
                if(str==''||str==null){
                    this.tipshow = true;
                    setTimeout(() => {
                        this.tipshow = false;
                    }, 1000);
                }else{
                    let date = new Date();
                    let year = date.getFullYear();
                    let month = date.getMonth()+1;
                    let showmonth = month<10?'0'+month:month;
                    let day = date.getDate();
                    let showday = day<10?'0'+day:day;
                    let hour = date.getHours();
                    let min = date.getMinutes();
                    let showmin = min<10?'0'+min:min;
                    let adddate = year+'-'+showmonth+'-'+showday;
                    let addtime = hour+':'+showmin;
                    this.updatecomment.date = adddate;
                    this.updatecomment.time = addtime;
                    let addcomment = Object.assign({}, this.updatecomment);
                    this.userpostlist[index].incomlist.cominf.unshift(addcomment);
                    this.updatecomment.content = '';
                    this.userpostlist[index].comment ++;
                    let pageNum = this.userpostlist[index].comment;
                    this.userpostlist[index].incomlist.totalPage = Math.ceil(pageNum/this.userpostlist[index].incomlist.pageSize);
                    this.userpostlist[index].incomlist.cominf.length = pageNum;
                    let page = this.userpostlist[index].incomlist.currentPage;
                    let start = this.userpostlist[index].incomlist.pageSize * page;
                    let end = this.userpostlist[index].incomlist.pageSize * (page+1);
                    this.userpostlist[index].incomlist.view = this.userpostlist[index].incomlist.cominf.slice(start,end);
                }
            }
        },
        atrecomd(name){
            document.getElementById("in-user-input").value = '@'+name+" : ";
            document.getElementById("in-user-input").focus();
        },
        setArticle([title,content,agree,collection,other,care,look,img,name,tag,put,recmd,grap,comment]){
            this.sarticle.title = title;
            this.sarticle.content = content;
            this.sarticle.agree = agree;
            this.sarticle.collection = collection;
            this.sarticle.other = other;
            this.sarticle.care = care;
            this.sarticle.look = look;
            this.sarticle.img = img;
            this.sarticle.name = name;
            this.sarticle.tag = tag;
            this.sarticle.put = put;
            this.sarticle.recmd = recmd;
            this.sarticle.grap = grap;
            this.sarticle.comment = comment;
            this.$store.commit('setArticle', [this.sarticle]);
        },
        changeNav(){
            this.$store.commit('setNavShow', [false]);
        }
    }
}
</script>

<style scoped>
@import '../static/css/Care.css';
</style>