<template>
    <div class="user-menu-box">
      <div class="login" v-if="userlogin.login">
        <div class="user-name">
          <h5>{{user.name}}</h5>
        </div>
        <div class="user-inf">
          <div class="text-box">
            <p class="title">{{moni.care}}</p>
            <p class="count">{{user.care}}</p>
          </div>
          <div class="text-box">
            <p class="title">{{moni.fans}}</p>
            <p class="count">{{user.fans}}</p>
          </div>
          <div class="text-box">
            <p class="title">{{moni.put}}</p>
            <p class="count">{{user.active}}</p>
          </div>
        </div>
        <div class="link-box">
            <div class="user-center">
              <i :class="moni.ucicon"></i>
              <router-link to="/Summary/Dynamic" style="color:#73C9E5" @click.native="changeNav()"><p>{{moni.ucenter}}</p></router-link>
            </div>
            <div class="user-put">
              <i :class="moni.upicon"></i>
              <router-link to="/VideoManager" style="color:#73C9E5" @click.native="changeNav()"><p>{{moni.uput}}</p></router-link>
            </div>
        </div>
        <div class="user-exit">
          <i :class="moni.eicon"></i>
          <router-link to="/Login" @click.native="setUserlogin(),changeNav()"><p>{{moni.exit}}</p></router-link>
        </div>
      </div>
      <div class="unlogin" v-if="userlogin.unlogin">
        <i :class="moni.loginicon"></i>
        <router-link to="/Login" @click.native="changeNav()"><p>登录</p></router-link>
      </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'Usermenu',
    data () {
      return {
        moni:{
          care:'关注',
          fans:'粉丝',
          put:'动态',
          ucenter:'个人中心',
          ucicon:'iconfont icon-user',
          uput:'投稿管理',
          upicon:'iconfont icon-tougao',
          exit:'退出',
          eicon:'iconfont icon-exit',
          loginicon:'iconfont icon-login-'
        },
        inuserlogin:{
          login:false,
          unlogin:true,
          bgimg:require('../assets/png/backimg.png'),
          tximg:require('../assets/png/unphoto.png'),
        },
        inuser:{
          name:'游客',
          tag:'未写下签名',
          sex:true,
          care:'0',
          fans:'0',
          looksum:'0',
          agreesum:'0',
          active:'0'
        },
      }
    },
    computed: {
      ...mapState(["userlogin","user","navshow"])
    },
    methods: {
      setUserlogin(){
        this.$store.commit('setLoginUser', [this.inuserlogin]);
        this.$store.commit('setUser', [this.inuser]);
      },
      changeNav(){
        this.$store.commit('setNavShow', [false]);
      }
    }
}
</script>

<style scoped>
@import '../static/css/Usermenu.css';
</style>