<template>
  <div id="app">
    <div class="nav" :style="{'background-color':maincolor}">
      <div class="appwidth">
        <router-link to="/" @click.native="Navchangestyle(0),changeNav()"><img :src="logo" class="logo" style="height:37.5px;width:120px"></router-link>
        <ul v-if="navshow">
            <li v-for="(item,index) in menu" :key="index">
              <router-link :to="item.url" :class="{'nav-link':navstyle==index}" @click.native="Navchangestyle(index)">{{item.title}}</router-link>
            </li>
        </ul>
        <div class="nav-right">
          <div class="search-box">
            <input type="text" class="search-input" id="search-input" v-model="searchinput">
            <button class="search-button">
              <router-link :to="searchurl" @click.native="Search()">
                <i :class="search"></i>
              </router-link>
            </button>
          </div>
          <span class="menu" @click="Changemoreshow"><i :class="hamburgermenu"></i></span>
          <img :src="userlogin.tximg" class="touxiang" @click="Changemenushow">
        </div>
      </div>
    </div>
    <div class="fixed-box"></div>
    <div class="bottom-nav" :style="{'--color':maincolor}" v-if="navshow">
      <ul>
          <li v-for="(item,index) in menu" :key="index">
            <router-link :to="item.url" @click.native="Navchangestyle(index)">
              <div class="bottom-color-box" :class="{'botton-nav-link':navstyle==index}">
                <i :class="item.icon"></i>
                <p>{{item.title}}</p> 
              </div>
            </router-link>
          </li>
      </ul>
    </div>
    <router-view></router-view>
    <Backcomeup></Backcomeup>
    <Usermenu v-if="menushow"></Usermenu>
    <Moremenu v-if="moreshow"></Moremenu>
  </div>
</template>

<script>
import {mapState} from "vuex"
import Backcomeup from './components/Backcomeup.vue'
import Usermenu from './components/Usermenu.vue'
import Moremenu from './components/Moremenu.vue'
export default {
  data () {
        return {
            logo:require('./assets/png/logo3d.png'),
            hamburgermenu:'iconfont icon-gengduo',
            search:'iconfont icon-faxianweixuanzhongxuanzhong',
            navstyle:0,
            menushow:false,
            moreshow:false,
            menu:[
              {
                title:'首页',
                url:'/Home/Main',
                icon:'iconfont icon-homefill'
              },
              {
                title:'集言',
                url:'/Group/Main',
                icon:'iconfont icon-alimessagesystem'
              },
              {
                title:'视界',
                url:'/Video',
                icon:'iconfont icon-video'
              }
            ],
            searchinput:'',
            searchurl:''
        }
    },
    computed: {
        ...mapState(["article","maincolor","userlogin","searchtext","navshow"])
    },
    created () {
      window.addEventListener("click",this.offmenu,true);
    },
    mounted () {
      document.getElementById('search-input').addEventListener("change",this.searchInput,true);
    },
    methods: {
      Navchangestyle(index){
        this.navstyle = index;
      },
      Changemenushow(){
        this.menushow = !this.menushow;
      },
      Changemoreshow(){
        this.moreshow = !this.moreshow;
      },
      offmenu(){
        this.moreshow = false;
        this.menushow = false;
      },
      searchInput(){
        if(this.searchinput == null||this.searchinput == ''){
          this.searchurl = '/';
        }else {
          this.searchurl = '/Search';
          this.$store.commit('setNavShow', [false]);
        }
      },
      Search(){
        if(this.searchinput != null){
          this.$store.commit('setSearch', [this.searchinput]);
          this.searchinput = null;
        }
      },
      changeNav(){
        this.$store.commit('setNavShow', [true]);
      }
    },
    components: {
      Backcomeup,
      Usermenu,
      Moremenu
    }
}
</script>

<style scoped>
@import './static/css/Nav.css';
</style>