<template>
    <div class="addarticle-post-box" :style="{'--color':maincolor}">
        <input type="text" class="put-title" placeholder="在此输入你的标题" v-model="postmessage.title">
        <div ref="editorContainer" class="put-content" id="put-content"></div>
        <div class="button-pos">
            <input type="button" value="发布" class="put-button" @click="PutPost()">
            <router-link :to="url" id="put-button" v-show="false"></router-link>
        </div>
        <div class="tip" v-show="tipshow">
            <p id="tip">发布成功</p>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import { createEditor,TOOLS,historyBackTool,historyForwardTool,insertObjectTool,Toolbar,headingTool,boldTool,italicTool,strikeThroughTool,underlineTool,olTool,ulTool,fontSizeTool,textIndentTool,colorTool,textBackgroundTool,insertParagraphBeforeTool,insertParagraphAfterTool,fontFamilyTool,linkTool, unlinkTool,imageTool,textAlignTool,tableTool,findTool,cleanTool } from '@textbus/textbus';
import '@textbus/textbus/bundles/textbus.min.css';

export default {
    name:'AddArticle',
    data () {
        return {
            tipshow:false,
            postmessage:{
                title:'',
                content:{
                    
                },
            },
            url:'/Home/Main'
        }
    },
    mounted () {
        const options = {
            providers: [{
                provide: TOOLS,
                useValue: [
                    [historyBackTool, historyForwardTool],
                    [insertObjectTool],
                    [headingTool],
                    [boldTool, italicTool, strikeThroughTool, underlineTool],
                    [olTool, ulTool],
                    [fontSizeTool, textIndentTool],
                    [colorTool, textBackgroundTool],
                    [insertParagraphBeforeTool, insertParagraphAfterTool],
                    [fontFamilyTool],
                    [linkTool, unlinkTool],
                    [imageTool],
                    [textAlignTool],
                    [tableTool],
                    [findTool],
                    [cleanTool]
                ]
            }],
            plugins: [
                Toolbar
            ],
        };
        const editor = createEditor(this.$refs.editorContainer,options);
        editor.onChange.subscribe(() => {
            this.postmessage.content = editor.getContents();
        });
    },
    computed: {
        ...mapState(['maincolor','navshow']),
    },
    methods: {
        PutPost(){
            if((this.postmessage.title == ''||this.postmessage.title == null)||(this.postmessage.content == ''||this.postmessage.content == null)){
                document.getElementById("tip").innerText = '标题或内容不能为空';
                this.tipshow = true;
                setTimeout(() => {
                    this.tipshow = false;
                },1000);
            }else{
                this.$https.post("http://rap2api.taobao.org/app/mock/288525/AddPost",this.postmessage).then((res) => {
                    if(res.data.success){
                        document.getElementById("tip").innerText = '发布成功';
                        this.tipshow = true;
                        setTimeout(() => {
                            this.tipshow = false;
                            this.$store.commit('setNavShow', [true]);
                            document.getElementById('put-button').click();
                        },1000);
                    }
                })
            }
        },
    }
}
</script>

<style scoped>
@import '../static/css/AddArticle.css';
</style>