<template>
    <div class="allbox">
        <div class="speak-all">
            <div class="speak-nav" :class="{'nav-fixed':scrollchange}">
                <ul>
                    <li v-for="(item,index) in navlist" :key="index">
                        <router-link :to="item.url">{{item.name}}</router-link>
                    </li>
                </ul>
            </div>
            <div :class="{'nav-box':navboxshow}"></div>
            <router-view></router-view>
        </div>
        <div class="speak-put" :class="{'put-fixed':scrollchange}">
            <div class="put-box">
                <div v-for="(item,index) in icon" :key="index">
                    <router-link :to="userlogin.login?item.url:'/Login'" @click.native="hideNav()"><span><i :class="item.iconfont"></i></span></router-link>
                    <p>{{item.text}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex"
export default {
    name:'Speakcontent',
    data () {
        return {
            navlist:[
                {
                    name:'七嘴八舌',
                    url:'/Home/Main'
                },
                {
                    name:'关注',
                    url:'/Home/Care'
                },
            ],
            icon:[
                {
                    iconfont:'iconfont icon-xiaoxi',
                    text:'发言',
                    url:'/Put'
                },
                {
                    iconfont:'iconfont icon-lightbulb',
                    text:'想法',
                    url:'/AddIdea'
                },
                {
                    iconfont:'iconfont icon-chuangzuo',
                    text:'文章',
                    url:'/AddArticle'
                }
            ],
            scrollchange:false,
            navboxshow:false,
        }
    },
    computed: {
        ...mapState(["navshow","userlogin"])
    },
    mounted () {
        window.addEventListener("scroll",this.Ifscroll,true);
    },
    methods: {
        Ifscroll(){
            let top = document.documentElement.scrollTop;
            top>=310?(this.scrollchange=true):(this.scrollchange=false);
            top>=310?(this.navboxshow=true):(this.navboxshow=false);
        },
        hideNav(){
            this.$store.commit('setNavShow', [false]);
        }
    }
}
</script>

<style scoped>
@import '../static/css/Speakcontent.css';
</style>