<template>
    <div class="list-collection" :style="{'--color':maincolor}">
        <ul>
            <li v-for="(li,index) in collection" :key="index">
                <router-link :to="li.url">
                    {{li.name}}
                </router-link>
            </li>
        </ul>
        <router-view></router-view>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'sumCollection',
    data () {
        return {
            collection:[
                {
                    name:'问答',
                    url:'/Summary/Collection/Post'
                },
                {
                    name:'视频',
                    url:'/Summary/Collection/Video'
                },
            ]
        }
    },
    computed: {
        ...mapState(['maincolor'])
    },
}
</script>

<style scoped>
@import '../static/css/sumCollection.css';
</style>