<template>
    <div class="put-vlist-box" :style="{'--color':maincolor}">
        <div class="put-inf-box" v-for="(list,index) in videolist" :key="index">
            <router-link :to="'/Video/'+list.vid" @click.native="setTitle([list.title,list.name,list.upimg]),changeNav()">
                <img :src="list.img" class="put-inf-img">
            </router-link>
            <div class="put-inf-text">
                <router-link :to="'/Video/'+list.vid" @click.native="setTitle([list.title,list.name,list.upimg]),changeNav()">
                    <p class="put-inf-text-title">{{list.title}}</p>
                </router-link>
                <div class="put-text-btm">
                    <div class="put-inf-text-user">
                        <img :src="list.upimg" class="put-inf-user-img">
                        <p class="put-inf-user-name">{{list.name}}</p>
                    </div>
                    <p class="put-inf-text-play">{{list.play}}万播放</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name:'Videolist',
    data () {
        return {
            videolist:[

            ]
        }
    },
    created () {
        
    },
    beforeMount () {
        this.getvideolist();
    },
    mounted () {
        this.scroll();
    },
    computed: {
        ...mapState(["videotitle","videoupname","videoupimg","navshow","maincolor"])
    },
    methods: {
        getvideolist(){
            for(let i=0;i<10;i++){
                this.$https.get("http://rap2api.taobao.org/app/mock/288525/videohotlist").then((res)=>{
                    this.videolist.push(res.data.hotlist[i]);
                })
            }
        },
        setTitle([title,name,img]){
            this.$store.commit("setTitle", [title,name,img]);
        },
        scroll(){
            let loading = false;
            window.onscroll = ()=>{
                let bottomOfWindow = document.documentElement.offsetHeight - document.documentElement.scrollTop - window.innerHeight <=200;
                if(bottomOfWindow && loading == false){
                    loading = true;
                    for(let i=0;i<6;i++){
                        this.$https.get("http://rap2api.taobao.org/app/mock/288525/videohotlist").then((res)=>{
                            this.videolist.push(res.data.hotlist[i]);
                        })
                    }
                    loading = false;
                }
            }
        },
        changeNav(){
            this.$store.commit('setNavShow', [false]);
        }
    }
}
</script>

<style scoped>
@import '../static/css/Videolist.css';
</style>