<template>
    <div class="mobie-inf-box" :style="{'--color':maincolor}">
        <div class="user-top-box">
            <div class="user-box">
                <div class="user-inf">
                    <img :src="videoupimg">
                    <div class="user-text">
                        <p class="name">{{videoupname}}</p>
                        <p class="tag">{{video.upspeak}}</p>
                    </div>
                </div>
                <div class="care">
                    <p>+</p>
                    关注
                </div>
            </div>
            <div class="video-inf-box">
                <p class="title">{{videotitle}}</p>
                <div class="icon-bar">
                    <div class="play">
                        <i :class="play"></i>
                        <p>{{video.vplaycount}}</p>
                    </div>
                    <p class="time">{{video.vupdate}}</p>
                </div>
                <div class="details">
                    <div class="summary" id="summary" @click="summaryChange()">展开简介</div>
                    <p id="sum-text" v-show="ifsummary">{{videoinf}}</p>
                </div>
                <div class="but-bar">
                    <div class="agree">
                        <i :class="video.agreeicon?beforeagree:agree" @click="clickvideoagree"></i>
                        <p>{{video.agree}}</p>
                    </div>
                    <div class="collection">
                        <i :class="video.collectionicon?beforecollection:collection" @click="clickvideocollection"></i>
                        <p>{{video.collection}}</p>
                    </div>
                    <div class="share">
                        <i :class="other" @click="clickvideoother"></i>
                        <p>{{video.other}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="tip-bar">
            <p v-for="(taglist,index) in videotaglist" :key="index">{{taglist.tag}}</p>
        </div>
        <div class="video-bar" v-for="(vlist,index) in revideolist" :key="index">
            <router-link to="/Video/Information" @click.native="setTitle([vlist.title,vlist.name,vlist.upimg])">
                <img :src="vlist.img">
            </router-link>
            <div class="video-inf-box">
                <p class="title">{{vlist.title}}</p>
                <div class="user-inf">
                    <p class="name">{{vlist.name}}</p>
                    <div class="play">
                        <i :class="play"></i>
                        <p>{{vlist.playcount}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    name:'MobieVideoInf',
    data () {
        return {
            touxiang:this.$store.state.userlogin.tximg,
            videoinf:'在1990年至1992年，《樱桃小丸子》第一次动画化，于富士电视台播出，并曾于1990年10月28日达到39.9%的收视率，在1991年9月26日成为有纪录以来日本史上最高收视的动画。而第二次动画化于1995年开始，直至2005年现仍播放中，并于2005年1月9日迎来了播放15周年纪念。',
            play:'iconfont icon-play',
            agree:'iconfont icon-dianzan',
            beforeagree:'iconfont icon-dianzan1',
            collection:'iconfont icon-shoucang',
            beforecollection:'iconfont icon-shoucangtianchong',
            other:'iconfont icon-fenxiang',
            ifsummary:false,
            video:{

            },
            revideolist:[

            ],
            videotaglist:[
                {
                    tag:"樱桃小丸子"
                },
                {
                    tag:"音乐"
                },
                {
                    tag:"童年"
                },
                {
                    tag:"日语"
                },
                {
                    tag:"美食"
                },
                {
                    tag:"经典"
                }
            ]
        }
    },
    computed: {
        ...mapState(["videotitle","videoupname","videoupimg","maincolor","userlogin","user"])
    },
    created () {
        this.getvideo();
        this.getrevideo();
    },
    methods: {
        summaryChange(){
            let btn = document.getElementById('summary');
            if(this.ifsummary == false){
                this.ifsummary = true;
                btn.innerHTML = '收起简介';
            }else if(this.ifsummary == true){
                this.ifsummary = false;
                btn.innerHTML = '展开简介';
            }
        },
        getvideo(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/video").then((res)=>{
                this.video = res.data.videoinf;
            })
        },
        clickvideoagree(){
            this.video.agreeicon = !this.video.agreeicon;
            if(this.video.agreeicon == true){
                this.video.agree++;
            }
            else{
                this.video.agree--;
            }
        },
        clickvideocollection(){
            this.video.collectionicon = !this.video.collectionicon;
            if(this.video.collectionicon == true){
                this.video.collection++;
            }
            else{
                this.video.collection--;
            }
        },
        clickvideoother(){
            if(this.video.othericon == false){
                this.video.other++;
                this.video.othericon = true;
            }
        },
        getrevideo(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/revideo").then((res)=>{
                this.revideolist = res.data.revideolist;
            })
        },
        setTitle([title,name,img]){
            document.documentElement.scrollTop = 0;
            this.$store.commit('setTitle', [title,name,img]);
            this.getvideo();
            this.getcomment();
            this.getrevideo();
            this.updatecomment.content = '';
        }
    }
}
</script>

<style scoped>
@import '../static/css/MobieVideoInf.css';
</style>