<template>
    <div class="search-page" :style="{'--color':maincolor}">
        <div class="search-left" v-if="textlist.comlist">
            <h3>搜索到关于"<p :style="{color:maincolor}">{{searchtext}}</p>"的{{textlist.comlist.length}}条文字记录</h3>
            <div class="search-box" v-for="(text,index) in textlist.view" :key="index">
                <router-link :to="'/Group/'+text.id" @click.native="setArticle([text.content,text.agree,text.collection,text.other,text.care,text.look,text.img,text.name,text.tag,text.put,text.recmd,text.grap,text.comment])">
                    <h5 :style="{'--color':maincolor}">{{text.tag}}<p :style="{color:maincolor}">{{searchtext}}</p>{{text.tag}}</h5>
                </router-link>
                <p>{{text.content}}</p>
            </div>
            <div class="page-box">
                <ul>
                    <li v-for="(num,liindex) in textlist.totalPage" :key="liindex" @click="Indexchangestyle(liindex)" :class="{'textindexcolor':textlist.indexstyle == liindex}">{{num}}</li>
                    <li @click="getMoreList">下一页</li>
                </ul>
            </div>
        </div>
        <div class="search-right" v-if="videolist.video">
            <h3>搜索到关于"<p :style="{color:maincolor}">{{searchtext}}</p>"的{{videolist.video.length}}条视频记录</h3>
            <div class="video-pos">
                <div class="search-video" v-for="(video,index) in videolist.view" :key="index">
                    <router-link :to="'/Video/'+video.id" @click.native="setTitle([video.title,video.name,video.userimg])">
                        <img :src="video.img">
                    </router-link>
                    <router-link :to="'/Video/'+video.id" @click.native="setTitle([video.title,video.name,video.userimg])">
                        <h5 :style="{'--color':maincolor}">{{video.title}}<p :style="{color:maincolor}">{{searchtext}}</p>{{video.title}}</h5>
                    </router-link>
                    <div class="play-inf">
                        <span>
                            <i :class="playicon"></i>
                            <p>{{video.play}}</p>
                        </span>
                        <span>
                            <i :class="timeicon"></i>
                            <p>{{video.date}}</p>
                        </span>
                    </div>
                    <span>
                        <i :class="usericon"></i>
                        <p>{{video.name}}</p>
                    </span>
                </div>
            </div>
            <div class="page-box">
                <ul>
                    <li v-for="(num,liindex) in videolist.totalPage" :key="liindex" @click="Videochangestyle(liindex)" :class="{'videoindexcolor':videolist.indexstyle == liindex}">{{num}}</li>
                    <li @click="getMoreVideo">下一页</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'Search',
    data () {
        return {
            playicon:'iconfont icon-play',
            timeicon:'iconfont icon-time',
            usericon:'iconfont icon-user',
            textlist:{

            },
            videolist:{

            },
            sarticle:{
                title:'',
                content:'',
                agree:'',
                collection:'',
                other:'',
                care:'',
                look:'',
                img:'',
                name:'',
                tag:'',
                put:'',
                recmd:'',
                grap:'',
                comment:''
            }
        }
    },
    created () {
        this.getTextList();
        this.getVideoList();
    },
    computed: {
        ...mapState(['searchtext','maincolor','article','videotitle','videoupname','videoupimg'])
    },
    methods: {
        getTextList(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/detaillist").then((res)=>{
                this.textlist = res.data.commentlist;
                this.textlist.totalPage = Math.ceil(this.textlist.comlist.length/this.textlist.pageSize);
                let page = this.textlist.currentPage;
                let start = this.textlist.pageSize * page;
                let end = this.textlist.pageSize * (page+1);
                this.textlist.view = this.textlist.comlist.slice(start,end);
            });
        },
        Indexchangestyle(liindex){
            this.textlist.indexstyle = liindex;
            let page = liindex;
            this.textlist.currentPage = page;
            let start = this.textlist.pageSize * page;
            let end = this.textlist.pageSize * (page+1);
            this.textlist.view = this.textlist.comlist.slice(start,end);
        },
        getMoreList(){
            if(this.textlist.currentPage<this.textlist.totalPage - 1){
                let page = this.textlist.currentPage;
                if(page+1>this.textlist.totalPage){
                    return false;
                }else{
                    page = page + 1;
                    this.textlist.currentPage = page;
                    this.textlist.indexstyle++;
                }
                let start = this.textlist.pageSize * page;
                let end = this.textlist.pageSize * (page+1);
                this.textlist.view = this.textlist.comlist.slice(start,end);
            }
        },
        getVideoList(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/searchvideolist").then((res)=>{
                this.videolist = res.data.videolist;
                this.videolist.totalPage = Math.ceil(this.videolist.video.length/this.videolist.pageSize);
                let page = this.videolist.currentPage;
                let start = this.videolist.pageSize * page;
                let end = this.videolist.pageSize * (page+1);
                this.videolist.view = this.videolist.video.slice(start,end);
            });
        },
        Videochangestyle(liindex){
            this.videolist.indexstyle = liindex;
            let page = liindex;
            this.videolist.currentPage = page;
            let start = this.videolist.pageSize * page;
            let end = this.videolist.pageSize * (page+1);
            this.videolist.view = this.videolist.video.slice(start,end);
        },
        getMoreVideo(){
            if(this.videolist.currentPage<this.videolist.totalPage - 1){
                let page = this.videolist.currentPage;
                if(page+1>this.videolist.totalPage){
                    return false;
                }else{
                    page = page + 1;
                    this.videolist.currentPage = page;
                    this.videolist.indexstyle++;
                }
                let start = this.videolist.pageSize * page;
                let end = this.videolist.pageSize * (page+1);
                this.videolist.view = this.videolist.video.slice(start,end);
            }
        },
        setArticle([content,agree,collection,other,care,look,img,name,tag,put,recmd,grap,comment]){
            this.sarticle.title = tag + this.$store.state.searchtext + tag;
            this.sarticle.content = content;
            this.sarticle.agree = agree;
            this.sarticle.collection = collection;
            this.sarticle.other = other;
            this.sarticle.care = care;
            this.sarticle.look = look;
            this.sarticle.img = img;
            this.sarticle.name = name;
            this.sarticle.tag = tag;
            this.sarticle.put = put;
            this.sarticle.recmd = recmd;
            this.sarticle.grap = grap;
            this.sarticle.comment = comment;
            this.$store.commit('setArticle', [this.sarticle]);
        },
        setTitle([title,name,img]){
            let intitle = title + this.$store.state.searchtext + title;
            this.$store.commit("setTitle", [intitle,name,img]);
        },
    }
}
</script>

<style scoped>
@import '../static/css/Search.css';
</style>