<template>
    <div class="detail-box" :style="{'--color':maincolor}">
        <div class="title-box">
            <div class="pos-box">
                <div class="content-box">
                    <h5 class="title">{{article.title}}</h5>
                    <pre class="content">{{article.content}}</pre>
                    <div class="icon-bar">
                        <div class="flow" :style="{'background-color':maincolor}">
                            <p>关注内容</p>
                        </div>
                        <div class="answer" @click="showAnswerbox" :style="{'color':maincolor,'border-color':maincolor}">
                            <p>写回答</p>
                        </div>
                        <div class="bar-box">
                            <i :class="agreeicon?beforeagree:agree" @click="clickAgree"></i><p>{{article.agree}}</p>
                            <i :class="collectionicon?beforecollection:collection" @click="clickCollection"></i><p>{{article.collection}}</p>
                            <i :class="othericon?other:other" @click="clickOther"></i><p>{{article.other}}</p>
                        </div>
                    </div>
                </div>
                <div class="post-box">
                    <div class="flow">
                        <p class="tip">关注</p>
                        <p class="number">{{article.care}}</p>
                    </div>
                    <div class="look">
                        <p class="tip">浏览</p>
                        <p class="number">{{article.look}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="answer-box">
            <div class="left-box">
                <div class="write-box" v-if="showanswer">
                    <div class="user-inf">
                        <img :src="userlogin.tximg">
                        <div class="user">
                            <p class="name">{{user.name}}</p>
                            <p class="self">{{user.tag}}</p>
                        </div>
                    </div>
                    <textarea id="user-input" class="input-box" :style="{'outline-color':maincolor}" v-model="updatalist.content"></textarea>
                    <button class="put" :style="{'background-color':maincolor}" @click="addViewList">发布</button>
                </div>
                <div class="box-answer">
                    <div class="navbar">
                        <h5>共{{article.comment}}条回答</h5>
                    </div>
                    <div class="answer-content" v-for="(comlist,index) in commentlist.view" :key="index">
                        <div class="user-inf">
                            <img :src="comlist.img">
                            <div class="user">
                                <p class="name">{{comlist.name}}</p>
                                <p class="self">{{comlist.tag}}</p>
                            </div>
                        </div>
                        <pre class="content">{{comlist.content}}</pre>
                        <p class="date">发布于{{comlist.date}}</p>
                        <div class="bar-box">
                            <i :class="comlist.agreeicon?beforeagree:agree" class="agree" @click="listclickagree(comlist.id,index)"></i><p>{{comlist.agree}}</p>
                            <i :class="comment" class="comment" @click="showcombar(comlist.id,index),getincom(comlist.comment,comlist.id,index)"></i><p>{{comlist.comment}}</p>
                        </div>
                        <div class="in-speak-bar" v-if="comlist.showcombar">
                            <h5>共{{comlist.comment}}条评论</h5>
                            <div v-for="(cmd,cindex) in comlist.incomlist.view" :key="cindex">
                                <div class="user-cmd-box">
                                    <div class="user-inf">
                                        <div class="user-imginf">
                                            <img :src="cmd.img">
                                            <p class="name">{{cmd.name}}</p>
                                        </div>
                                        <p class="date">{{cmd.date}}</p>
                                    </div>
                                    <div class="comd-content">
                                        <pre class="content">{{cmd.content}}</pre>
                                    </div>
                                    <div class="in-icon-bar">
                                        <i :class="cmd.agreeicon?beforeagree:agree" @click="clickinagree(cmd.id,cindex,index)"></i><p>{{cmd.agree}}</p>
                                        <i :class="comment" @click="atrecomd(cmd.name)"></i>
                                    </div>
                                </div>
                            </div>
                            <div class="page-box">
                                <ul>
                                    <li>共{{comlist.incomlist.totalPage}}页</li>
                                    <li v-for="(num,liindex) in comlist.incomlist.totalPage" :key="liindex" :class="{'spkindexcolor':comlist.incomlist.indexstyle == liindex}" @click="Indexchangestyle(comlist.comment,comlist.id,index,liindex)">{{num}}</li>
                                    <li @click="Nextchangestyle(comlist.comment,comlist.id,index)">下一页</li>
                                </ul>
                            </div>
                            <div class="in-user-comment">
                                <textarea id="in-user-input" class="in-user-input" v-model="inupdatacomment.content"  :style="{'outline-color':maincolor}"></textarea>
                                <button class="in-user-inputbtn" :style="{'background-color':maincolor}" @click="addincomment(comlist.id,index)"><p>发表</p><br><p>评论</p></button>
                            </div>
                        </div>
                    </div>
                    <div class="load-more">
                        <p :style="{color:maincolor}" @click="getMoreList()" id="load-text">浏览更多</p>
                    </div>
                </div>
                <div class="tip-nonull" v-if="tipshow">
                    <p>回复的内容不能一个字也没有哦</p>
                </div>
            </div>
            <div class="right-box">
                <h5>关于作者</h5>
                <div class="user-inf">
                    <img :src="article.img">
                    <div class="user">
                        <p class="name">{{article.name}}</p>
                        <p class="self">{{article.tag}}</p>
                    </div>
                </div>
                <div class="userinf-box">
                    <div class="put">
                        <p class="tip">发帖</p>
                        <p class="number">{{article.put}}</p>
                    </div>
                    <div class="answer">
                        <p class="tip">回复</p>
                        <p class="number">{{article.recmd}}</p>
                    </div>
                    <div class="text">
                        <p class="tip">文章</p>
                        <p class="number">{{article.grap}}</p>
                    </div>
                </div>
                <button class="care" :style="{'background-color':maincolor}">关注</button>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex"
export default {
    name:'detailSpeak',
    data () {
        return {
            agree:'iconfont icon-dianzan',
            beforeagree:'iconfont icon-dianzan1',
            collection:'iconfont icon-shoucang',
            beforecollection:'iconfont icon-shoucangtianchong',
            other:'iconfont icon-fenxiang',
            comment:'iconfont icon-_ico_reply',
            showanswer:false,
            touxiang:require("../assets/png/avatar/草莓.png"),
            agreeicon:false,
            collectionicon:false,
            othericon:false,
            tipshow:false,
            commentlist:{

            },
            updatalist:{
                id:'1',
                img:this.$store.state.userlogin.tximg,
                name:this.$store.state.user.name,
                tag:this.$store.state.user.tag,
                content:'',
                date:'',
                agree:'0',
                comment:'0',
                agreeicon:false,
                showcombar:false,
                incomlist:{
                    pageSize:'6',
                    currentPage:'0',
                    totalPage:'0',
                    indexstyle:'0',
                    view:[

                    ],
                    cominf:[

                    ]
                },
            },
            inupdatacomment:{
                img: this.$store.state.userlogin.tximg,
                name: this.$store.state.user.name,
                content: '',
                date: '',
                time: '',
                agree: 0,
                agreeicon: false,
                id: 1
            },
        }
    },
    created () {
       this.getComList();
    },
    mounted () {
        
    },
    computed: {
        ...mapState(["article","maincolor","userlogin","user"])
    },
    methods: {
        showAnswerbox(){
            this.showanswer = !this.showanswer;
        },
        clickAgree(){
            this.agreeicon = !this.agreeicon;
            if(this.agreeicon == true){
                this.$store.state.article.agree++;
            }else {
                this.$store.state.article.agree--;
            }
        },
        clickCollection(){
            this.collectionicon = !this.collectionicon;
            if(this.collectionicon == true){
                this.$store.state.article.collection++;
            }else {
                this.$store.state.article.collection--;
            }
        },
        clickOther(){
            this.othericon = !this.othericon;
            if(this.othericon == true){
                this.$store.state.article.other++;
            }else {
                this.$store.state.article.other--;
            }
        },
        getComList(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/detaillist").then((res)=>{
                this.commentlist = res.data.commentlist;
                this.commentlist.totalPage = Math.ceil(this.$store.state.article.comment/this.commentlist.pageSize);
                this.commentlist.comlist.length = this.$store.state.article.comment;
                let page = this.commentlist.currentPage;
                let start = this.commentlist.pageSize * page;
                let end = this.commentlist.pageSize * (page+1);
                this.commentlist.view = this.commentlist.comlist.slice(start,end);
            });
        },
        getMoreList(){
            let page = this.commentlist.currentPage;
            if(this.commentlist.currentPage<this.commentlist.totalPage - 1){
                page = page + 1;
                this.commentlist.currentPage = page;
                let start = this.commentlist.pageSize * page;
                let end = this.commentlist.pageSize * (page+1);
                let add = this.commentlist.comlist.slice(start,end);
                for(let i=0;i<add.length;i++){
                    this.commentlist.view.push(add[i]);
                }
            }else{
                document.getElementById("load-text").innerHTML = "已经到底啦";
            }
        },
        listclickagree(id,index){
            if(this.commentlist.view[index].id == id){
                this.commentlist.view[index].agreeicon = !this.commentlist.view[index].agreeicon;
                if(this.commentlist.view[index].agreeicon == true){
                    this.commentlist.view[index].agree++;
                }else {
                    this.commentlist.view[index].agree--;
                }
            }
        },
        addViewList(){
            let input = document.getElementById("user-input").value;
            let str = input.replace(/(^\s*)|(\s*$)/g, "");
            if(str==''||str==null){
                this.tipshow = true;
                setTimeout(() => {
                    this.tipshow = false;
                }, 1000);
            }else{
                let date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth()+1;
                let showmonth = month<10?'0'+month:month;
                let day = date.getDate();
                let showday = day<10?'0'+day:day;
                let adddate = year+'-'+showmonth+'-'+showday;
                this.updatalist.date = adddate;
                let addcomment = Object.assign({}, this.updatalist);
                this.commentlist.view.unshift(addcomment);
                this.updatalist.content = '';
                this.$store.state.article.comment++;
            }
        },
        showcombar(id,index){
            if(this.commentlist.view[index].id == id){
                this.commentlist.view[index].showcombar = !this.commentlist.view[index].showcombar;
            }
        },
        getincom(pageNum,id,index){
            if(this.commentlist.view[index].id == id){
                this.$https.get("http://rap2api.taobao.org/app/mock/288525/detailinlist").then((res)=>{
                    this.commentlist.view[index].incomlist = res.data.comlist;
                    this.commentlist.view[index].incomlist.totalPage = Math.ceil(pageNum/this.commentlist.view[index].incomlist.pageSize);
                    this.commentlist.view[index].incomlist.cominf.length = pageNum;
                    let page = this.commentlist.view[index].incomlist.currentPage;
                    let start = this.commentlist.view[index].incomlist.pageSize * page;
                    let end = this.commentlist.view[index].incomlist.pageSize * (page+1);
                    this.commentlist.view[index].incomlist.view = this.commentlist.view[index].incomlist.cominf.slice(start,end);
                });
            }
        },
        Indexchangestyle(pageNum,id,index,liindex){
            if(this.commentlist.view[index].id == id){
                this.commentlist.view[index].incomlist.indexstyle = liindex;
                this.commentlist.view[index].incomlist.cominf.length = pageNum;
                let page = liindex;
                let start = this.commentlist.view[index].incomlist.pageSize * page;
                let end = this.commentlist.view[index].incomlist.pageSize * (page+1);
                this.commentlist.view[index].incomlist.view = this.commentlist.view[index].incomlist.cominf.slice(start,end);
            }
        },
        Nextchangestyle(pageNum,id,index){
            if(this.commentlist.view[index].id == id){
                if(this.commentlist.view[index].incomlist.indexstyle<(this.commentlist.view[index].incomlist.totalPage - 1)){
                    this.commentlist.view[index].incomlist.indexstyle++;
                    this.commentlist.view[index].incomlist.cominf.length = pageNum;
                    let page = this.commentlist.view[index].incomlist.currentPage;
                    if(page+1>this.commentlist.view[index].incomlist.totalPage){
                        return false;
                    }else{
                        page = page + 1;
                        this.commentlist.view[index].incomlist.currentPage = page;
                    }
                    let start = this.commentlist.view[index].incomlist.pageSize * page;
                    let end = this.commentlist.view[index].incomlist.pageSize * (page+1);
                    this.commentlist.view[index].incomlist.view = this.commentlist.view[index].incomlist.cominf.slice(start,end);
                }
            }
        },
        clickinagree(id,cindex,index){
            if(this.commentlist.view[index].incomlist.view[cindex].id == id){
                this.commentlist.view[index].incomlist.view[cindex].agreeicon = !this.commentlist.view[index].incomlist.view[cindex].agreeicon;
                if(this.commentlist.view[index].incomlist.view[cindex].agreeicon == true){
                    this.commentlist.view[index].incomlist.view[cindex].agree++;
                }
                else{
                    this.commentlist.view[index].incomlist.view[cindex].agree--;
                }
            }
        },
        addincomment(id,index){
            if(this.commentlist.view[index].id == id){
                let input = document.getElementById("in-user-input").value;
                let str = input.replace(/(^\s*)|(\s*$)/g, "");
                if(str==''||str==null){
                    this.tipshow = true;
                    setTimeout(() => {
                        this.tipshow = false;
                    }, 1000);
                }else{
                    let date = new Date();
                    let year = date.getFullYear();
                    let month = date.getMonth()+1;
                    let showmonth = month<10?'0'+month:month;
                    let day = date.getDate();
                    let showday = day<10?'0'+day:day;
                    let hour = date.getHours();
                    let min = date.getMinutes();
                    let showmin = min<10?'0'+min:min;
                    let adddate = year+'-'+showmonth+'-'+showday;
                    let addtime = hour+':'+showmin;
                    this.inupdatacomment.date = adddate;
                    this.inupdatacomment.time = addtime;
                    let addcomment = Object.assign({}, this.inupdatacomment);
                    this.commentlist.view[index].incomlist.cominf.unshift(addcomment);
                    this.inupdatacomment.content = '';
                    this.commentlist.view[index].comment ++;
                    let pageNum = this.commentlist.view[index].comment;
                    this.commentlist.view[index].incomlist.totalPage = Math.ceil(pageNum/this.commentlist.view[index].incomlist.pageSize);
                    this.commentlist.view[index].incomlist.cominf.length = pageNum;
                    let page = this.commentlist.view[index].incomlist.currentPage;
                    let start = this.commentlist.view[index].incomlist.pageSize * page;
                    let end = this.commentlist.view[index].incomlist.pageSize * (page+1);
                    this.commentlist.view[index].incomlist.view = this.commentlist.view[index].incomlist.cominf.slice(start,end);
            }
            }
        },
        atrecomd(name){
            document.getElementById("in-user-input").value = '@'+name+" : ";
            document.getElementById("in-user-input").focus();
        },
    }
}
</script>

<style scoped>
@import '../static/css/detailSpeak.css';
</style>