<template>
    <div class="pos-box">
        <div class="v-left-box">
            <p class="p-reput">推荐视频</p>
            <div class="reput-box">
                <div class="video-box" v-for="(video,index) in videolist" :key="index">
                    <div class="hover-box">
                        <router-link class="video-link" :to="'/Video/'+video.vid" @click.native="setTitle([video.title,video.name,video.upimg]),changeNav()">
                            <div class="hover-img">
                                <i :class="play"></i>
                            </div>
                        </router-link>
                        <router-link class="video-link" :to="'/Video/'+video.vid" @click.native="setTitle([video.title,video.name,video.upimg]),changeNav()">
                            <img :src="video.img" class="video-img">
                        </router-link>
                    </div>
                    <p class="video-title">{{video.title}}</p>
                    <div class="video-up-box">
                        <img :src="video.upimg" class="up-img">
                        <p class="video-name">{{video.name}}</p>
                    </div>
                </div>
            </div>
            <div class="more-text-box">
                <router-link to='/Morevideo/Self'>
                    <p class="p-more">更多视频</p>
                </router-link>
            </div>
            <p class="p-hot">热门视频</p>
            <div class="hot-box">
                <div class="video-box" v-for="(video,index) in videolist" :key="index">
                    <div class="hover-box">
                        <router-link class="video-link" :to="'/Video/'+video.vid" @click.native="setTitle([video.title,video.name,video.upimg]),changeNav()">
                            <div class="hover-img">
                                <i :class="play"></i>
                            </div>
                        </router-link>
                        <router-link class="video-link" :to="'/Video/'+video.vid" @click.native="setTitle([video.title,video.name,video.upimg]),changeNav()">
                            <img :src="video.img" class="video-img">
                        </router-link>
                    </div>
                    <p class="video-title">{{video.title}}</p>
                    <div class="video-up-box">
                        <img :src="video.upimg" class="up-img">
                        <p class="video-name">{{video.name}}</p>
                    </div>
                </div>
            </div>
            <div class="more-text-box">
                <router-link to='/Morevideo/Hot'>
                    <p class="p-more">更多视频</p>
                </router-link>
            </div>
        </div>
        <div class="v-right-box">
            <router-link :to="userlogin.login?'/PutVideo':'/login'" @click.native="changeNav()">
                <button class="btn-upvideo">视频投稿</button>
            </router-link>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name:'Videorecommend',
    data () {
        return {
            play:'iconfont icon-play',
            videolist:[

            ]
        }
    },
    created () {
        this.getputvideo();
    },
    watch: {
        $route(){
            this.$route.params.videoid = this.videolist.vid;
        }
    },
    computed: {
        ...mapState(["videotitle","videoupname","videoupimg","navshow","userlogin"])
    },
    methods: {
        getputvideo(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/videoput").then((res)=>{
                this.videolist = res.data.videolist;
            })
        },
        setTitle([title,name,img]){
            this.$store.commit("setTitle", [title,name,img]);
        },
        changeNav(){
            this.$store.commit('setNavShow', [false]);
        }
    }
}
</script>

<style scoped>
@import '../static/css/Videorecommend.css';
</style>