<template>
  <div class="carousel">
      <swiper :options="swiperOptions" class="swiper">
        <swiper-slide class="swiper-slide" v-for="(img,index) in imglist" :key="index">
          <img class="swiper-img" :src="img.url">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
    name:'Carousel',
    data () {
      return {
        imglist:[
          {
            id:1,
            url:require('../assets/png/hiyoo海报01.png')
          },
          {
            id:2,
            url:require('../assets/png/hiyoo海报01.png')
          },
          {
            id:3,
            url:require('../assets/png/hiyoo海报01.png')
          }
        ],
        swiperOptions:{
          pagination:'swiper-pagination',
          loop:true,
          autoplay:true,
          effect: "coverflow",
          grabCursor: true,
          centeredSlides: true,
          slidesPerView: "3",
          coverflowEffect: {
            rotate: 0,
            stretch: 0,
            depth: 300,
            modifier: 1,
            slideShadows: false,
        },
        },
      }
    }
    
}
</script>

<style scoped>
@import '../static/css/Carousel.css';
</style>