<template>
    <div class="moremenu-box" :style="{'--color':maincolor}">
        <div class="skin">
            <div class="title">
                <i :class="color"></i>
                <p>网站皮肤</p>
            </div>
            <div class="skin-color">
                <div style="background-color: #ff9502"></div><p @click="setOrgin">橙色主题</p>
            </div>
            <div class="skin-color">
                <div style="background-color: #ff8888"></div><p @click="setcolor">粉色主题</p>
            </div>
        </div>
        <div class="menu-put" v-show="userlogin.login">
            <div class="title">
                <i :class="put"></i>
                <p>发布内容</p>
            </div>
            <div class="put-pos-box">
                <router-link to="/Put" class="title" @click.native="changeNav()">
                    <div class="put-box">
                        <i :class="post"></i>
                        <p>贴子</p>
                    </div>
                </router-link>
                <router-link to="/AddIdea" class="title" @click.native="changeNav()">
                    <div class="put-box">
                        <i :class="idea"></i>
                        <p>想法</p>
                    </div>
                </router-link>
                <router-link to="/AddArticle" class="title" @click.native="changeNav()">
                    <div class="put-box">
                        <i :class="article"></i>
                        <p>文章</p>
                    </div>
                </router-link>
                <router-link to="/PutVideo" class="title" @click.native="changeNav()">
                    <div class="put-box">
                        <i :class="video"></i>
                        <p>视频</p>
                    </div>
                </router-link>
            </div>
        </div>
        <div class="set">
            <router-link to="/Setting" class="title" @click.native="changeNav()">
                <i :class="setting"></i>
                <p>设置</p>
            </router-link>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex"
export default {
    name:"Moremenu",
    data () {
        return {
            color:"iconfont icon-color",
            setting:"iconfont icon-set",
            put:'iconfont icon-fabu',
            post:'iconfont icon-xiaoxi',
            idea:'iconfont icon-lightbulb',
            article:'iconfont icon-chuangzuo',
            video:'iconfont icon-play'
        }
    },
    computed: {
        ...mapState(["maincolor","navshow","userlogin"])
    },
    methods: {
        setcolor(){
            this.$store.commit('setPink', ['#ff8888']);
        },
        setOrgin(){
            this.$store.commit('setOrgin', ['#FF9502']);
        },
        changeNav(){
            this.$store.commit('setNavShow', [false]);
        }
    }
}
</script>

<style scoped>
@import '../static/css/Moremenu.css';
</style>