<template>
    <div class="answer-box" :style="{'--color':maincolor}">
        <div class="left-box">
            <div class="tips" v-for="(dynamic,index) in dynamiclist.view" :key="index">
                <div class="user">
                    <img :src="hiyoomanager">
                    <div class="user-inf">
                        <p class="name">{{user.name}}</p>
                        <p class="date">{{dynamic.date}}</p>
                    </div>
                </div>
                <router-link :to="'/Group/'+dynamic.id" @click.native="setArticle([dynamic.title,dynamic.content,dynamic.agree,dynamic.collection,dynamic.share,dynamic.care,dynamic.look,dynamic.put,dynamic.recmd,dynamic.grap,dynamic.comment,dynamic.userimg,dynamic.name,dynamic.tag])">
                    <pre class="dynamic-text">{{dynamic.content}}</pre>
                </router-link>
                <div class="by">
                    <p>BY</p>
                    <router-link :to="'/Group/'+dynamic.id" @click.native="setArticle([dynamic.title,dynamic.content,dynamic.agree,dynamic.collection,dynamic.share,dynamic.care,dynamic.look,dynamic.put,dynamic.recmd,dynamic.grap,dynamic.comment,dynamic.userimg,dynamic.name,dynamic.tag])">
                        <h3 class="dynamic-title">{{dynamic.title}}</h3>
                    </router-link>
                </div>
            </div>
            <div class="load-more">
                <p @click="getMoreList()" id="load-text">浏览更多</p>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'sumAnswer',
    data () {
        return {
            hiyoomanager:require('../assets/png/avatar/草莓.png'),
            dynamiclist:{

            },
            sarticle:{
                title:'',
                content:'',
                agree:'',
                collection:'',
                other:'',
                care:'',
                look:'',
                img:'',
                name:'',
                tag:'',
                put:'',
                recmd:'',
                grap:'',
                comment:''
            }
        }
    },
    created () {
        this.getDynamicList();
    },
    computed: {
        ...mapState(['searchtext','maincolor','article','videotitle','videoupname','videoupimg','user'])
    },
    methods: {
        getDynamicList(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/dynamic").then((res)=>{
                this.dynamiclist = res.data.dynamiclist;
                this.dynamiclist.totalPage = Math.ceil(this.dynamiclist.dynamic.length/this.dynamiclist.pageSize);
                let page = this.dynamiclist.currentPage;
                let start = this.dynamiclist.pageSize * page;
                let end = this.dynamiclist.pageSize * (page+1);
                this.dynamiclist.view = this.dynamiclist.dynamic.slice(start,end);
            })
        },
        getMoreList(){
            let page = this.dynamiclist.currentPage;
            if(this.dynamiclist.currentPage<this.dynamiclist.totalPage - 1){
                page = page + 1;
                this.dynamiclist.currentPage = page;
                let start = this.dynamiclist.pageSize * page;
                let end = this.dynamiclist.pageSize * (page+1);
                let add = this.dynamiclist.dynamic.slice(start,end);
                for(let i=0;i<add.length;i++){
                    this.dynamiclist.view.push(add[i]);
                }
            }else{
                document.getElementById("load-text").innerHTML = "已经到底啦";
            }
        },
        setArticle([title,content,agree,collection,other,care,look,put,recmd,grap,comment,userimg,name,tag]){
            this.sarticle.title = title;
            this.sarticle.content = content;
            this.sarticle.agree = agree;
            this.sarticle.collection = collection;
            this.sarticle.other = other;
            this.sarticle.care = care;
            this.sarticle.look = look;
            this.sarticle.img = userimg;
            this.sarticle.name = name;
            this.sarticle.tag = tag;
            this.sarticle.put = put;
            this.sarticle.recmd = recmd;
            this.sarticle.grap = grap;
            this.sarticle.comment = comment;
            this.$store.commit('setArticle', [this.sarticle]);
        },
    }
}
</script>

<style scoped>
@import '../static/css/sumAnswer.css';
</style>