<template>
    <div class="into-box">
        <div class="login-box" v-show="login">
            <div class="logo">
                <img :src="logo">
                <p>账号登陆</p>
            </div>
            <div class="input">
                <div class="remind" v-show="remind">
                    <i :class="warningicon"></i>
                    <p id="premind"></p>
                </div>
                <input type="text" class="text" id="text" autocomplete="off" placeholder="请输入邮箱或手机号" v-model="logininf.id">
                <input type="password" class="pw" id="pw" autocomplete="off" placeholder="请输入密码" v-model="logininf.pw">
                <router-link :to="url" class="submit-link" id="submit-link"></router-link>
                <button class="submit" @click="setUserlogin()">登录</button>
            </div>
            <div class="change-nav">
                <router-link to="/Login/tip">
                    <p>忘记密码</p>
                </router-link>
                <p @click="regshow()">立即注册</p>
            </div>
        </div>
        <div class="reg-box" v-show="reg">
            <div class="logo">
                <img :src="logo">
                <p>注册</p>
            </div>
            <div class="input">
                <div class="remind" v-show="rremind">
                    <i :class="warningicon"></i>
                    <p id="rpremind"></p>
                </div>
                <input type="text" class="text" id="rtext" autocomplete="off" placeholder="请输入邮箱或手机号" v-model="reginf.id">
                <input type="text" class="name" id="name" autocomplete="off" placeholder="请输入昵称" v-model="reginf.name">
                <input type="password" class="pw" id="rpw" autocomplete="off" placeholder="请输入密码" v-model="reginf.pw">
                <input type="submit" class="submit" value="注册" @click="reguser">
            </div>
            <div class="change-nav">
                <p @click="loginshow()">登录</p>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'Login',
    data () {
        return {
            logo:require('../assets/png/logo3d.png'),
            warningicon:'iconfont icon-warning',
            login:true,
            reg:false,
            remind:false,
            rremind:false,
            url:'/',
            inuserlogin:{
                login:false,
                unlogin:true,
                bgimg:require('../assets/png/hiyoobg.jpg'),
                tximg:require('../assets/png/avatar/草莓.png'),
            },
            inuser:{
                name:'',
                tag:'',
                sex:'',
                care:'',
                fans:'',
                looksum:'',
                agreesum:'',
                active:''
            },
            logininf:{
                id:'',
                pw:'',
            },
            reginf:{
                id:'',
                name:'',
                pw:''
            }
        }
    },
    mounted () {
        document.getElementById("text").addEventListener('blur',this.inputremind,false),
        document.getElementById("pw").addEventListener('blur',this.pwremind,false)
    },
    updated () {
        this.remindreg()
    },
    computed: {
        ...mapState(["userlogin","user","navshow"])
    },
    methods: {
        loginshow(){
            this.login = true,
            this.reg = false
        },
        regshow(){
            this.reg = true,
            this.login = false
        },
        remindreg(){
            if(this.reg == true){
                document.getElementById("rtext").addEventListener('blur',this.reinputremind,false),
                document.getElementById("rpw").addEventListener('blur',this.repwremind,false),
                document.getElementById("name").addEventListener('blur',this.nameremind,false)   
            }
        },
        setUserlogin(){
            if(this.logininf.id != '' && this.logininf.pw != ''){
                this.inuserlogin.login = true;
                this.inuserlogin.unlogin = false;
                this.$store.commit('setLoginUser', [this.inuserlogin]);
                this.$https.get("http://rap2api.taobao.org/app/mock/288525/user").then((res)=>{
                    this.inuser = res.data.user;
                    this.$store.commit('setUser', [this.inuser]);
                });
                this.$store.commit('setNavShow', [true]);
                document.getElementById('submit-link').click();
            }
        },
        reguser(){
            if(this.reginf.id != '' && this.reginf.pw != '' && this.reginf.name != ''){
                this.login = true,
                this.reg = false,
                this.reginf.id = '',
                this.reginf.pw = '',
                this.reginf.name = ''
            }
        },
        inputremind(){
            if(this.login == true){
                if(this.logininf.id == ''){
                    document.getElementById('premind').innerHTML = '账号不能为空';
                    this.remind = true;
                }else{
                    this.remind = false;
                }
            }
        },
        pwremind(){
            if(this.login == true){
                if(this.logininf.pw == ''){
                    document.getElementById('premind').innerHTML = '密码应该在8-12个字符之间';
                    this.remind = true;
                }else{
                    this.remind = false;
                }
            }
        },
        reinputremind(){
            if(this.reg == true){
                if(this.reginf.id == ''){
                    document.getElementById('rpremind').innerHTML = '账号不能为空';
                    this.rremind = true;
                }else{
                    this.rremind = false;
                }
            }
        },
        repwremind(){
            if(this.reg == true){
                if(this.reginf.pw == ''){
                    document.getElementById('rpremind').innerHTML = '密码应该在8-12个字符之间';
                    this.rremind = true;
                }else{
                    this.rremind = false;
                }
            }
        },
        nameremind(){
            if(this.reg == true){
                if(this.reginf.name == ''){
                    document.getElementById('rpremind').innerHTML = '昵称不能为空';
                    this.rremind = true;
                }else{
                    this.rremind = false;
                }
            }
        }
    }
}
</script>

<style scoped>
@import '../static/css/Login.css';
</style>