<template>
    <div class="setting-box" :style="{'--color':maincolor}">
        <div class="content-box">
            <div class="button-box">
                <h5>设置</h5>
                <button>保存更改</button>
            </div>
            <div class="main-box">
                <h5 class="theme">主题</h5>
                <div class="input">
                    <div class="check-box">
                        <input type="checkbox" name="oragin" id="oragin" checked="checked" @click="oraginCheck()">
                        <label for="oragin">橙色(默认)</label>
                    </div>
                    <div class="check-box">
                        <input type="checkbox" name="pink" id="pink" @click="pinkCheck()">
                        <label for="pink">粉色</label>
                    </div>
                </div>
                <h5 class="animation">动画和访问</h5>
                <div class="input">
                    <div class="check-box">
                        <input type="checkbox" name="gif" id="gif" checked="checked">
                        <label for="gif">自动播放GIF动画</label>
                    </div>
                    <div class="check-box">
                        <input type="checkbox" name="font" id="font">
                        <label for="font">使用系统默认字体</label>
                    </div>
                </div>
                <h5 class="home">主页</h5>
                <div class="input">
                    <div class="check-box">
                        <input type="checkbox" name="business" id="business" checked="checked">
                        <label for="business">显示广告和推荐</label>
                    </div>
                </div>
                <h5 class="truedalog">确认对话框</h5>
                <div class="input">
                    <div class="check-box">
                        <input type="checkbox" name="care" id="care" checked="checked">
                        <label for="care">在取消关注前询问我</label>
                    </div>
                    <div class="check-box">
                        <input type="checkbox" name="toother" id="toother" checked="checked">
                        <label for="toother">在转发前询问我</label>
                    </div>
                    <div class="check-box">
                        <input type="checkbox" name="delete" id="delete" checked="checked">
                        <label for="delete">在删除前询问我</label>
                    </div>
                </div>
                <button>保存更改</button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    name:'Setting',
    computed: {
        ...mapState(["maincolor"])
    },
    methods: {
        oraginCheck(){
            let oragin = document.getElementById('oragin');
            let pink = document.getElementById('pink');
            pink.checked = false;
            oragin.checked = true;
        },
        pinkCheck(){
            let oragin = document.getElementById('oragin');
            let pink = document.getElementById('pink');
            oragin.checked = false;
            pink.checked = true;
        }
    }
}
</script>

<style scoped>
@import '../static/css/Setting.css';
</style>