<template>
    <div class="sumvideo-box" :style="{'--color':maincolor}">
        <div class="left-box">
            <div class="tips" v-for="(dynamic,index) in dynamiclist.view" :key="index">
                <div class="user">
                    <img :src="userimg">
                    <div class="user-inf">
                        <p class="name">{{user.name}}</p>
                        <p class="date">{{dynamic.date}}</p>
                    </div>
                </div>
                <div class="video-inf">
                    <router-link class="video-img" :to="'/Video/'+dynamic.id" @click.native="setTitle([dynamic.title,dynamic.name,dynamic.userimg])">
                        <img :src="dynamic.img">
                    </router-link>
                    <div class="video-text">
                        <router-link :to="'/Video/'+dynamic.id" @click.native="setTitle([dynamic.title,dynamic.name,dynamic.userimg])">
                            <h3>{{dynamic.title}}</h3>
                        </router-link>
                        <pre>{{dynamic.text}}</pre>
                        <div class="icon-bar">
                            <i :class="play"></i>
                            <p>{{dynamic.play}}</p>
                        </div>
                    </div>
                </div>
                <div class="iconbar">
                    <span>
                        <i :class="other" @click="clickshare(dynamic.id,index)"></i>
                        <p>{{dynamic.share}}</p>
                    </span>
                    <span>
                        <i :class="dynamic.agreeicon?beforeagree:agree" class="agree" @click="clickagree(dynamic.id,index)"></i>
                        <p>{{dynamic.agree}}</p>
                    </span>
                </div>
            </div>
            <div class="load-more">
                <p @click="getMoreList()" id="load-text">浏览更多</p>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'sumVideo',
    data () {
        return {
            play:'iconfont icon-play',
            agree:'iconfont icon-dianzan',
            beforeagree:'iconfont icon-dianzan1',
            other:'iconfont icon-fenxiang',
            userimg:require("../assets/png/avatar/草莓.png"),
            dynamiclist:{

            },
            sarticle:{
                title:'',
                content:'',
                agree:'',
                collection:'',
                other:'',
                care:'',
                look:'',
                img:'',
                name:'',
                tag:'',
                put:'',
                recmd:'',
                grap:'',
                comment:''
            }
        }
    },
    created () {
        this.getDynamicList();
    },
    computed: {
        ...mapState(['searchtext','maincolor','article','videotitle','videoupname','videoupimg','user'])
    },
    methods: {
        getDynamicList(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/dynamic").then((res)=>{
                this.dynamiclist = res.data.dynamiclist;
                this.dynamiclist.totalPage = Math.ceil(this.dynamiclist.dynamic.length/this.dynamiclist.pageSize);
                let page = this.dynamiclist.currentPage;
                let start = this.dynamiclist.pageSize * page;
                let end = this.dynamiclist.pageSize * (page+1);
                this.dynamiclist.view = this.dynamiclist.dynamic.slice(start,end);
            })
        },
        getMoreList(){
            let page = this.dynamiclist.currentPage;
            if(this.dynamiclist.currentPage<this.dynamiclist.totalPage - 1){
                page = page + 1;
                this.dynamiclist.currentPage = page;
                let start = this.dynamiclist.pageSize * page;
                let end = this.dynamiclist.pageSize * (page+1);
                let add = this.dynamiclist.dynamic.slice(start,end);
                for(let i=0;i<add.length;i++){
                    this.dynamiclist.view.push(add[i]);
                }
            }else{
                document.getElementById("load-text").innerHTML = "已经到底啦";
            }
        },
        clickshare(id,index){
            if(this.dynamiclist.view[index].id == id){
                this.dynamiclist.view[index].shareicon = !this.dynamiclist.view[index].shareicon;
                if(this.dynamiclist.view[index].shareicon == true){
                    this.dynamiclist.view[index].share++;
                }else {
                    this.dynamiclist.view[index].share--;
                }
            }
        },
        clickagree(id,index){
            if(this.dynamiclist.view[index].id == id){
                this.dynamiclist.view[index].agreeicon = !this.dynamiclist.view[index].agreeicon;
                if(this.dynamiclist.view[index].agreeicon == true){
                    this.dynamiclist.view[index].agree++;
                }else {
                    this.dynamiclist.view[index].agree--;
                }
            }
        },
        setTitle([title,name,img]){
            this.$store.commit("setTitle", [title,name,img]);
        },
    }
}
</script>

<style scoped>
@import '../static/css/sumVideo.css';
</style>