import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    videotitle:'',
    videoupname:'',
    videoupimg:'',
    article:{
      
    },
    maincolor:'#FF9502',
    userlogin:{
      login:false,
      unlogin:true,
      bgimg:require('../assets/png/backimg.png'),
      tximg:require('../assets/png/unphoto.png'),
    },
    user:{
      name:'游客',
      tag:'未填写签名',
      sex:true,
      care:'',
      fans:'',
      looksum:'',
      agreesum:'',
      active:'',
    },
    searchtext:'',
    navshow:true
  },
  mutations: {
    setTitle(state,[title,name,img]){
      state.videotitle = title;
      state.videoupname = name;
      state.videoupimg = img;
    },
    setArticle(state,[article]){
      let obj = Object.assign({},article);
      state.article = obj;
    },
    setLoginUser(state,[userlogin]){
      let obj = Object.assign({},userlogin);
      state.userlogin = obj;
    },
    setUser(state,[user]){
      let obj = Object.assign({},user);
      state.user = obj;
    },
    setPink(state,[pink]){
      state.maincolor = pink;
    },
    setOrgin(state,[orgin]){
      state.maincolor = orgin;
    },
    setSearch(state,[searchtext]){
      state.searchtext = searchtext;
    },
    setNavShow(state,[navshow]){
      state.navshow = navshow;
    },
  },
  getters: {

  },
  actions: {
    
  },
  modules: {
    
  }
})
