<template>
    <div class="video-mng-box" :style="{'--color':maincolor}">
        <div class="video-list">
            <div class="video-box" v-for="(list,index) in videolist.view" :key="index">
                <div class="time">
                    <div class="time-text">
                        <p>{{list.time}}</p>
                    </div>
                    <div class="pot"></div>
                </div>
                <div class="video-line-box">
                    <div class="line-box">
                        <div class="dot"></div>
                        <div class="line"></div>
                    </div>
                    <div class="video">
                        <div class="img-box">
                            <img :src="list.img" class="img">
                            <div class="text-box">
                                <p class="title">{{list.title}}</p>
                                <p class="content">{{list.content}}</p>
                            </div>
                        </div>
                        <div class="button-box">
                            <button>删除</button>
                            <button>修改</button>
                        </div>
                        <div class="mobie-button-box">
                            <i :class="change" class="change"></i>
                            <i :class="deleteicon" class="deleteicon"></i>
                        </div>
                    </div>
                </div>
            </div>
            <div class="page-box">
                <ul>
                    <li v-for="(num,liindex) in videolist.totalPage" :key="liindex" @click="Indexchangestyle(liindex)" :class="{'videomngindexcolor':videolist.indexstyle == liindex}">{{num}}</li>
                    <li @click="getMoreVideoList">下一页</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    name:'VideoManager',
    data () {
        return {
            change:'iconfont icon-xiugai',
            deleteicon:'iconfont icon-deletefill',
            videolist:{

            }
        }
    },
    computed: {
        ...mapState(['maincolor'])
    },
    created () {
        this.getVideoList();
    },
    methods: {
        getVideoList(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/VideoManagerList").then((res) => {
                this.videolist = res.data.videolist;
                this.videolist.totalPage = Math.ceil(this.videolist.list.length/this.videolist.pageSize);
                let page = this.videolist.currentPage;
                let start = this.videolist.pageSize * page;
                let end = this.videolist.pageSize * (page+1);
                this.videolist.view = this.videolist.list.slice(start,end);
            })
        },
        Indexchangestyle(liindex){
            this.videolist.indexstyle = liindex;
            let page = liindex;
            this.videolist.currentPage = page;
            let start = this.videolist.pageSize * page;
            let end = this.videolist.pageSize * (page+1);
            this.videolist.view = this.videolist.list.slice(start,end);
        },
        getMoreVideoList(){
            if(this.videolist.currentPage<this.videolist.totalPage - 1){
                let page = this.videolist.currentPage;
                if(page+1>this.videolist.totalPage){
                    return false;
                }else{
                    page = page + 1;
                    this.videolist.currentPage = page;
                    this.videolist.indexstyle++;
                }
                let start = this.videolist.pageSize * page;
                let end = this.videolist.pageSize * (page+1);
                this.videolist.view = this.videolist.list.slice(start,end);
            }
        }
    }
}
</script>

<style scoped>
@import '../static/css/VideoManager.css';
</style>