<template>
    <div class="mobie-cmt-box">
        <div class="cmt-box">
            <div class="title-box">
                <p class="title">全部评论</p>
                <p class="number">{{commentlist.length}}</p>
            </div>
            <div class="cmt-bar" v-for="(comment,index1) in commentlist" :key="index1">
                <img :src="comment.img" class="user-img">
                <div class="user-inf-pos">
                    <p class="name">{{comment.name}}</p>
                    <pre class="cmt-content">{{comment.content}}</pre>
                    <div class="but-bar">
                        <div class="inf-box">
                            <p>#{{index1+1}}</p>
                            <p class="date">{{comment.date}}</p>
                            <p class="time">{{comment.time}}</p>
                        </div>
                        <div class="icon-bar">
                            <div class="agree">
                                <i :class="comment.agreeicon?beforeagree:agree" @click="clickcomagree(comment.id,index1)"></i><p>{{comment.agree}}</p>
                            </div>
                            <div class="comment">
                                <i :class="iconcomment" @click="commentIcon(comment.id,index1,comment.comment)"></i>
                            </div>
                            <p class="comeback" @click="showcombar(comment.id,index1)" v-if="comment.showcombar">收起</p>
                        </div>
                    </div>
                    <div class="show-cmt-box" v-if="!comment.showcombar">
                        <div v-for="(list,index) in comment.incomlist.view" :key="index">
                            <p class="show-text"><span>{{list.name}}</span>: {{list.content}}</p>
                        </div>
                        <p class="more-cmt" @click="showcombar(comment.id,index1),getincom(comment.comment,comment.id,index1)">共{{comment.comment}}条回复</p>
                    </div>
                    <div class="in-cmt-box" v-if="comment.showcombar">
                        <div class="in-comment-box" v-for="(list,index) in comment.incomlist.view" :key="index">
                            <img :src="list.img" class="in-user-img">
                            <div class="in-user-box">
                                <p class="name">{{list.name}}</p>
                                <div class="in-comment-content">
                                    <pre class="in-content">{{list.content}}</pre>
                                    <div class="icon-bar">
                                        <div class="date-box">
                                            <p class="comment-date">{{list.date}}</p>
                                            <p class="comment-time">{{list.time}}</p>
                                        </div>
                                        <div class="right-icon">
                                            <i :class="list.agreeicon?beforeagree:agree" @click="clickinagree(list.id,index,index1)"></i><p>{{list.agree}}</p>
                                            <p class="in-comment-p" @click="atrecomd(list.name),focusInput()">回复</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="page-box">
                            <ul>
                                <li>共{{comment.incomlist.totalPage}}页</li>
                                <li v-for="(num,index) in comment.incomlist.totalPage" :key="index" :class="{'mobieindexcolor':comment.incomlist.indexstyle == index}" @click="Indexchangestyle(comment.comment,comment.id,index1,index)">{{num}}</li>
                                <li @click="Nextchangestyle(comment.comment,comment.id,index1)">下一页</li>
                            </ul>
                        </div>
                        <div class="in-user-comment">
                            <textarea id="in-user-input" class="in-user-input" v-model="inupdatecomment.content"></textarea>
                            <button class="in-user-inputbtn" @click="addincomment(comment.id,index1)">发送</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="space-box" v-show="!incmtbar"></div>
        <div class="input-bar" v-show="!incmtbar">
            <textarea id="user-input" class="user-input" v-model="updatecomment.content"></textarea>
            <button @click="addcomment()">发送</button>
        </div>
        <div class="tip-null" v-if="tipshow">
            <p>回复的内容不能一个字也没有哦</p>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name:'MobieVideoCmt',
    data () {
        return {
            touxiang:this.$store.state.userlogin.tximg,
            agree:'iconfont icon-dianzan',
            beforeagree:'iconfont icon-dianzan1',
            iconcomment:'iconfont icon-_ico_reply',
            liststyle:'0',
            indexstyle:'0',
            tipshow:false,
            incmtbar:false,
            commentlist:[

            ],
            updatecomment:{
                id:'1',
                img:this.$store.state.userlogin.tximg,
                name:this.$store.state.user.name,
                content:'',
                date:'',
                time:'',
                agree:'0',
                comment:'0',
                agreeicon:false,
                commenticon:false,
                showcombar:false,
                incomlist:{
                    pageSize: 5,
                    currentPage: 0,
                    totalPage: 0,
                    indexstyle: 0,
                    view: [

                    ],
                    cominf: [
                       
                    ]
                }
            },
            inupdatecomment:{
                img: this.$store.state.userlogin.tximg,
                name: this.$store.state.user.name,
                content: '',
                date: '',
                time: '',
                agree: 0,
                agreeicon: false,
                id: 1
            }
        }
    },
    computed: {
        ...mapState(["videotitle","videoupname","videoupimg","maincolor","userlogin","user"])
    },
    created () {
        this.getcomment();
    },
    methods: {
        getcomment(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/videocomment").then((res)=>{
                this.commentlist = res.data.commentlist;
            })
        },
        getincom(pageNum,id,index){
            if(this.commentlist[index].id == id){
                this.$https.get("http://rap2api.taobao.org/app/mock/288525/incomment").then((res)=>{
                    this.commentlist[index].incomlist = res.data.comlist;
                    this.commentlist[index].incomlist.totalPage = Math.ceil(pageNum/this.commentlist[index].incomlist.pageSize);
                    this.commentlist[index].incomlist.cominf.length = pageNum;
                    let page = this.commentlist[index].incomlist.currentPage;
                    let start = this.commentlist[index].incomlist.pageSize * page;
                    let end = this.commentlist[index].incomlist.pageSize * (page+1);
                    this.commentlist[index].incomlist.view = this.commentlist[index].incomlist.cominf.slice(start,end);
                });
            }
        },
        clickcomagree(id,index){
            if(this.commentlist[index].id == id){
                this.commentlist[index].agreeicon = !this.commentlist[index].agreeicon;
                if(this.commentlist[index].agreeicon == true){
                    this.commentlist[index].agree++;
                }
                else{
                    this.commentlist[index].agree--;
                }
            }
        },
        clickinagree(id,index,index1){
            if(this.commentlist[index1].incomlist.view[index].id == id){
                this.commentlist[index1].incomlist.view[index].agreeicon = !this.commentlist[index1].incomlist.view[index].agreeicon;
                if(this.commentlist[index1].incomlist.view[index].agreeicon == true){
                    this.commentlist[index1].incomlist.view[index].agree++;
                }
                else{
                    this.commentlist[index1].incomlist.view[index].agree--;
                }
            }
        },
        clickliststyle(index){
            this.liststyle = index;
        },
        showcombar(id,index){
            if(this.commentlist[index].id == id){
                this.commentlist[index].showcombar = !this.commentlist[index].showcombar;
                this.incmtbar = !this.incmtbar;
            }
        },
        addcomment(){
            let input = this.updatecomment.content;
            let str = input.replace(/(^\s*)|(\s*$)/g, "");
            if(str==''||str==null){
                this.tipshow = true;
                setTimeout(() => {
                    this.tipshow = false;
                }, 1000);
            }else{
                let date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth()+1;
                let showmonth = month<10?'0'+month:month;
                let day = date.getDate();
                let showday = day<10?'0'+day:day;
                let hour = date.getHours();
                let min = date.getMinutes();
                let showmin = min<10?'0'+min:min;
                let adddate = year+'-'+showmonth+'-'+showday;
                let addtime = hour+':'+showmin;
                this.updatecomment.date = adddate;
                this.updatecomment.time = addtime;
                let addcomment = Object.assign({}, this.updatecomment);
                this.commentlist.unshift(addcomment);
                this.updatecomment.content = '';
            }
        },
        Indexchangestyle(pageNum,id,index1,index){
            if(this.commentlist[index1].id == id){
                this.commentlist[index1].incomlist.indexstyle = index;
                this.commentlist[index1].incomlist.cominf.length = pageNum;
                let page = index;
                let start = this.commentlist[index1].incomlist.pageSize * page;
                let end = this.commentlist[index1].incomlist.pageSize * (page+1);
                this.commentlist[index1].incomlist.view = this.commentlist[index1].incomlist.cominf.slice(start,end);
            }
        },
        Nextchangestyle(pageNum,id,index1){
            if(this.commentlist[index1].id == id){
                if(this.commentlist[index1].incomlist.indexstyle<(this.commentlist[index1].incomlist.totalPage - 1)){
                    this.commentlist[index1].incomlist.indexstyle++;
                    this.commentlist[index1].incomlist.cominf.length = pageNum;
                    let page = this.commentlist[index1].incomlist.currentPage;
                    if(page+1>this.commentlist[index1].incomlist.totalPage){
                        return false;
                    }else{
                        page = page + 1;
                        this.commentlist[index1].incomlist.currentPage = page;
                    }
                    let start = this.commentlist[index1].incomlist.pageSize * page;
                    let end = this.commentlist[index1].incomlist.pageSize * (page+1);
                    this.commentlist[index1].incomlist.view = this.commentlist[index1].incomlist.cominf.slice(start,end);
                }
            }
        },
        addincomment(id,index){
            if(this.commentlist[index].id == id){
                let input = this.inupdatecomment.content;
                let str = input.replace(/(^\s*)|(\s*$)/g, "");
                if(str==''||str==null){
                    this.tipshow = true;
                    setTimeout(() => {
                        this.tipshow = false;
                    }, 1000);
                }else{
                    let date = new Date();
                    let year = date.getFullYear();
                    let month = date.getMonth()+1;
                    let showmonth = month<10?'0'+month:month;
                    let day = date.getDate();
                    let showday = day<10?'0'+day:day;
                    let hour = date.getHours();
                    let min = date.getMinutes();
                    let showmin = min<10?'0'+min:min;
                    let adddate = year+'-'+showmonth+'-'+showday;
                    let addtime = hour+':'+showmin;
                    this.inupdatecomment.date = adddate;
                    this.inupdatecomment.time = addtime;
                    let addcomment = Object.assign({}, this.inupdatecomment);
                    this.commentlist[index].incomlist.cominf.unshift(addcomment);
                    this.inupdatecomment.content = '';
                    this.commentlist[index].comment ++;
                    let pageNum = this.commentlist[index].comment;
                    this.commentlist[index].incomlist.totalPage = Math.ceil(pageNum/this.commentlist[index].incomlist.pageSize);
                    this.commentlist[index].incomlist.cominf.length = pageNum;
                    let page = this.commentlist[index].incomlist.currentPage;
                    let start = this.commentlist[index].incomlist.pageSize * page;
                    let end = this.commentlist[index].incomlist.pageSize * (page+1);
                    this.commentlist[index].incomlist.view = this.commentlist[index].incomlist.cominf.slice(start,end);
                }
            }
        },
        focusInput(){
            let text =document.getElementById('in-user-input');
            text.focus();
        },
        atrecomd(name){
            this.inupdatecomment.content = '@'+name+" : ";
        },
        commentIcon(id,index,pageNum){
            if(this.commentlist[index].id == id){
                if(this.commentlist[index].showcombar == false){
                    this.commentlist[index].showcombar = true;
                    this.incmtbar = true;
                    this.$https.get("http://rap2api.taobao.org/app/mock/288525/incomment").then((res)=>{
                        this.commentlist[index].incomlist = res.data.comlist;
                        this.commentlist[index].incomlist.totalPage = Math.ceil(pageNum/this.commentlist[index].incomlist.pageSize);
                        this.commentlist[index].incomlist.cominf.length = pageNum;
                        let page = this.commentlist[index].incomlist.currentPage;
                        let start = this.commentlist[index].incomlist.pageSize * page;
                        let end = this.commentlist[index].incomlist.pageSize * (page+1);
                        this.commentlist[index].incomlist.view = this.commentlist[index].incomlist.cominf.slice(start,end);
                        this.focusInput();
                    });
                }else if(this.commentlist[index].showcombar == true){
                    this.focusInput();
                }
            }
        }
    }
}
</script>

<style scoped>
@import '../static/css/MobieVideoCmt.css';
</style>