<template>
    <div class="sum-box" :style="{'--color':maincolor}">
        <div class="sum-top">
            <div class="back-img">
                <div class="img-show-box">
                    <img :src="userlogin.bgimg">
                </div>
                <div class="change-img" @click="changeBgimg">
                    <p>编辑背景图片</p>
                    <input type="file" id="bg-img" @change="uploadBgimg" v-show="false" accept="image/jpg,image/png,image/jpeg">
                </div>
                <div class="user-img">
                    <div class="avatar-hover">
                        <div class="change-avatar" @click="changeAvatarimg">
                            <p>修改头像</p>
                        </div>
                        <img :src="userlogin.tximg">
                    </div>
                    <input type="file" id="avatar-img" @change="uploadAvatarimg" v-show="false" accept="image/jpg,image/png,image/jpeg">
                    <div class="user-inf">
                        <div class="name-box">
                            <p class="name" id="name">{{user.name}}</p>
                            <p class="tag" id="tag">{{user.tag}}</p>
                        </div>
                        <i :class="user.sex?female:man" v-if="!sexbar"></i>
                        <div class="sex-bar" v-if="sexbar">
                            <ul>
                                <li @click="showChoesSex"><i :class="user.sex?female:man"></i></li>
                                <li v-if="chosesex" @click="choesfemale"><i :class="female"></i></li>
                                <li v-if="chosesex" @click="choesman"><i :class="man"></i></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div class="change-user" :style="{'color':maincolor,'border-color':maincolor}">
                <p id="change-user" @click="changeUserInf">编辑资料</p>
            </div>
            <div class="nav-box">
                <div class="sum-nav">
                    <ul>
                        <li v-for="(li,index) in nav" :key="index">
                            <router-link :to="li.url" :style="{'--color':maincolor}" @click.native="Navchangestyle(index)">
                                <div class="li-color-box" :class="{'li-nav-link':navstyle==index}">
                                    {{li.title}}
                                </div>
                            </router-link>
                        </li>
                    </ul>
                </div>
                <div class="care-inf">
                    <div class="inf-box">
                        <p class="title">关注数</p>
                        <p class="number">{{user.care}}</p>
                    </div>
                    <div class="inf-box">
                        <p class="title">订阅数</p>
                        <p class="number">{{user.fans}}</p>
                    </div>
                </div>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'Summary',
    data () {
        return {
            female:'iconfont icon-nv',
            man:'iconfont icon-nan',
            change:false,
            sexbar:false,
            chosesex:false,
            navstyle:0,
            nav:[
                {
                    title:'动态',
                    url:'/Summary/Dynamic'
                },
                {
                    title:'关注',
                    url:'/Summary/Care'
                },
                {
                    title:'收藏',
                    url:'/Summary/Collection/Post'
                },
                {
                    title:'回答',
                    url:'/Summary/Answer'
                },
                {
                    title:'提问',
                    url:'/Summary/Put'
                },
                {
                    title:'文章',
                    url:'/Summary/Article'
                },
                {
                    title:'想法',
                    url:'/Summary/Idea'
                },
                {
                    title:'视频',
                    url:'/Summary/Video'
                },
            ]
        }
    },
    computed: {
        ...mapState(['maincolor','userlogin','user'])
    },
    methods: {
        Navchangestyle(index){
            this.navstyle = index;
        },
        changeUserInf(){
            let name = document.getElementById('name');
            let tag = document.getElementById('tag');
            let changebutton = document.getElementById('change-user');
            if(this.change == false){
                name.setAttribute('contenteditable',true);
                tag.setAttribute('contenteditable',true);
                this.change = true;
                this.sexbar = true;
                changebutton.innerText = '完成修改';
            }else if(this.change == true){
                name.setAttribute('contenteditable',false);
                tag.setAttribute('contenteditable',false);
                this.change = false;
                this.sexbar = false;
                this.chosesex = false;
                this.$store.state.user.name = name.innerText;
                this.$store.state.user.tag = tag.innerText;
                changebutton.innerText = '编辑资料';
            }
        },
        showChoesSex(){
            if(this.chosesex == false){
                this.chosesex = true;
            }else if(this.chosesex == true){
                this.chosesex = false;
            }
        },
        choesfemale(){
            this.$store.state.user.sex = true;
            this.chosesex =false;
        },
        choesman(){
            this.$store.state.user.sex = false;
            this.chosesex =false;
        },
        changeBgimg(){
            document.getElementById('bg-img').click();
        },
        uploadBgimg(){
            let img = document.getElementById('bg-img').files[0];
            let url = window.URL.createObjectURL(img);
            this.$store.state.userlogin.bgimg = url;
        },
        changeAvatarimg(){
            document.getElementById('avatar-img').click();
        },
        uploadAvatarimg(){
            let img = document.getElementById('avatar-img').files[0];
            let url = window.URL.createObjectURL(img);
            this.$store.state.userlogin.tximg = url;
        }
    }
}
</script>

<style scoped>
@import '../static/css/Summary.css';
</style>