<template>
    <div class="article-box" :style="{'--color':maincolor}">
        <div class="change" @click="ArticleChange">
            <p id="change">修改</p>
        </div>
        <h3>{{article.title}}</h3>
        <pre class="art-text" id="art-text">{{inarticle.content}}</pre>
        <div class="user-inf">
            <img :src="userimg">
            <div class="user-text">
                <div class="user">
                    <p class="name">{{user.name}}</p>
                    <p class="tag">{{user.tag}}</p>
                </div>
                <p class="time">{{inarticle.date}}</p>
            </div>
        </div>
        <div class="cmdlist">
            <div class="title-bar">
                <h4>共{{inarticle.articlecmdlist.length}}条评论</h4>
                <span @click="showcmd" id="add">+</span>
            </div>
            <div class="list" v-for="(list,index) in inarticle.view" :key="index">
                <img :src="list.img">
                <div class="user-text">
                    <p class="name">{{list.name}}</p>
                    <p class="tag">{{list.tag}}</p>
                    <pre>{{list.content}}</pre>
                    <p class="time">{{list.date}}</p>
                </div>
            </div>
            <div class="cmd-box" v-if="cmdshow">
                <textarea id="article-cmd" v-model="updatecomment.content"></textarea>
                <button @click="addcomment">发布</button>
            </div>
            <ul>
                <li>共{{inarticle.totalPage}}页</li>
                <li v-for="(num,index) in inarticle.totalPage" :key="index" @click="Indexchangestyle(index)" :class="{'licolor':inarticle.indexstyle == index}">{{num}}</li>
                <li @click="Nextchangestyle">下一页</li>
            </ul>
        </div>
        <div class="tip-nonull" v-if="tipshow">
            <p>回复的内容不能一个字也没有哦</p>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex"
export default {
    name:'Article',
    data () {
        return {
            inarticle:{

            },
            cmdshow:false,
            tipshow:false,
            userimg:require('../assets/png/avatar/草莓.png'),
            change:false,
            updatecomment:{
                id:'1',
                img:this.$store.state.userlogin.tximg,
                name:this.$store.state.user.name,
                tag:this.$store.state.user.tag,
                content:'',
                date:'',
            },
        }
    },
    created () {
        this.getArticle();
    },
    computed: {
        ...mapState(["article","maincolor","userlogin","user","article"])
    },
    methods: {
        getArticle(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/Article").then((res)=>{
                this.inarticle = res.data.Article;
                this.inarticle.totalPage = Math.ceil(this.inarticle.articlecmdlist.length/this.inarticle.pageSize);
                let page = this.inarticle.currentPage;
                let start = this.inarticle.pageSize * page;
                let end = this.inarticle.pageSize * (page+1);
                this.inarticle.view = this.inarticle.articlecmdlist.slice(start,end);
            })
        },
        showcmd(){
            this.cmdshow = !this.cmdshow;
            if(this.cmdshow == true){
                document.getElementById('add').innerHTML = '-';
            }else {
                document.getElementById('add').innerHTML = '+';
            }
        },
        Indexchangestyle(index){
            this.inarticle.indexstyle = index;
            let page = index;
            this.inarticle.currentPage = page;
            let start = this.inarticle.pageSize * page;
            let end = this.inarticle.pageSize * (page+1);
            this.inarticle.view = this.inarticle.articlecmdlist.slice(start,end);
        },
        Nextchangestyle(){
            if(this.inarticle.indexstyle<(this.inarticle.totalPage - 1)){
                this.inarticle.indexstyle++;
                let page = this.inarticle.currentPage;
                if(page+1>this.inarticle.totalPage){
                    return false;
                }else{
                    page = page + 1;
                    this.inarticle.currentPage = page;
                }
                let start = this.inarticle.pageSize * page;
                let end = this.inarticle.pageSize * (page+1);
                this.inarticle.view = this.inarticle.articlecmdlist.slice(start,end);
            }
        },
        addcomment(){
            let input = document.getElementById("article-cmd").value;
            let str = input.replace(/(^\s*)|(\s*$)/g, "");
            if(str==''||str==null){
                this.tipshow = true;
                setTimeout(() => {
                    this.tipshow = false;
                }, 1000);
            }else{
                let date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth()+1;
                let showmonth = month<10?'0'+month:month;
                let day = date.getDate();
                let showday = day<10?'0'+day:day;
                let adddate = year+'-'+showmonth+'-'+showday;
                this.updatecomment.date = adddate;
                let addcomment = Object.assign({}, this.updatecomment);
                this.inarticle.articlecmdlist.unshift(addcomment);
                this.updatecomment.content = '';
                this.inarticle.totalPage = Math.ceil(this.inarticle.articlecmdlist.length/this.inarticle.pageSize);
                let page = this.inarticle.currentPage;
                let start = this.inarticle.pageSize * page;
                let end = this.inarticle.pageSize * (page+1);
                this.inarticle.view = this.inarticle.articlecmdlist.slice(start,end);
            }
        },
        ArticleChange(){
            let text = document.getElementById("art-text");
            let button = document.getElementById("change");
            if(this.change == false){
                text.setAttribute('contenteditable',true);
                button.innerText = "完成";
                text.focus();
                this.change = true;
            }else if(this.change == true){
                button.innerText = "修改";
                text.setAttribute('contenteditable',false);
                this.change = false;
            }
        }
    }
}
</script>

<style scoped>
@import '../static/css/Article.css';
</style>