<template>
    <div class="idea-box" :style="{'--color':maincolor}">
        <div class="left-box">
            <div class="tips" v-for="(dynamic,index) in dynamiclist.view" :key="index">
                <div class="user">
                    <img :src="hiyoomanager">
                    <div class="user-inf">
                        <p class="name">{{user.name}}</p>
                        <p class="date">{{dynamic.date}}</p>
                    </div>
                </div>
                <router-link :to="'/Think/'+dynamic.id" @click.native="setArticle([dynamic.content,dynamic.agree,dynamic.share,dynamic.img,dynamic.name,dynamic.tag])">
                    <pre class="dynamic-text">{{dynamic.content}}</pre>
                </router-link>
                <div class="icon-bar">
                    <span>
                        <i :class="other" @click="clickshare(dynamic.id,index)"></i>
                        <p>{{dynamic.share}}</p>
                    </span>
                    <span>
                        <i :class="dynamic.agreeicon?beforeagree:agree" class="agree" @click="clickagree(dynamic.id,index)"></i>
                        <p>{{dynamic.agree}}</p>
                    </span>
                </div>
            </div>
            <div class="load-more">
                <p @click="getMoreList()" id="load-text">浏览更多</p> 
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'sumIdea',
    data () {
        return {
            hiyoomanager:require('../assets/png/avatar/草莓.png'),
            agree:'iconfont icon-dianzan',
            beforeagree:'iconfont icon-dianzan1',
            other:'iconfont icon-fenxiang',
            dynamiclist:{

            },
            sarticle:{
                title:'',
                content:'',
                agree:'',
                collection:'',
                other:'',
                care:'',
                look:'',
                img:'',
                name:'',
                tag:'',
                put:'',
                recmd:'',
                grap:'',
                comment:''
            }
        }
    },
    created () {
        this.getDynamicList();
    },
    computed: {
        ...mapState(['searchtext','maincolor','article','videotitle','videoupname','videoupimg','user'])
    },
    methods: {
        getDynamicList(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/Article").then((res)=>{
                this.dynamiclist = res.data.Article;
                this.dynamiclist.totalPage = Math.ceil(this.dynamiclist.articlecmdlist.length/this.dynamiclist.pageSize);
                let page = this.dynamiclist.currentPage;
                let start = this.dynamiclist.pageSize * page;
                let end = this.dynamiclist.pageSize * (page+1);
                this.dynamiclist.view = this.dynamiclist.articlecmdlist.slice(start,end);
            })
        },
        getMoreList(){
            let page = this.dynamiclist.currentPage;
            if(this.dynamiclist.currentPage<this.dynamiclist.totalPage - 1){
                page = page + 1;
                this.dynamiclist.currentPage = page;
                let start = this.dynamiclist.pageSize * page;
                let end = this.dynamiclist.pageSize * (page+1);
                let add = this.dynamiclist.dynamic.slice(start,end);
                for(let i=0;i<add.length;i++){
                    this.dynamiclist.view.push(add[i]);
                }
            }else{
                document.getElementById("load-text").innerHTML = "已经到底啦";
            }
        },
        setArticle([content,agree,share,img,name,tag]){
            this.sarticle.content = content;
            this.sarticle.agree = agree;
            this.sarticle.other = share;
            this.sarticle.img = img;
            this.sarticle.name = name;
            this.sarticle.tag = tag;
            this.$store.commit('setArticle', [this.sarticle]);
        },
        clickshare(id,index){
            if(this.dynamiclist.view[index].id == id){
                this.dynamiclist.view[index].shareicon = !this.dynamiclist.view[index].shareicon;
                if(this.dynamiclist.view[index].shareicon == true){
                    this.dynamiclist.view[index].share++;
                }else {
                    this.dynamiclist.view[index].share--;
                }
            }
        },
        clickagree(id,index){
            if(this.dynamiclist.view[index].id == id){
                this.dynamiclist.view[index].agreeicon = !this.dynamiclist.view[index].agreeicon;
                if(this.dynamiclist.view[index].agreeicon == true){
                    this.dynamiclist.view[index].agree++;
                }else {
                    this.dynamiclist.view[index].agree--;
                }
            }
        },
    }
}
</script>

<style scoped>
@import '../static/css/sumIdea.css';
</style>