<template>
    <div class="video-all-box" :style="{'--color':maincolor}">
        <div class="left-box">
            <div class="video-play-box">
                <div class="title-box">
                    <h3>{{videotitle}}</h3>
                    <div class="play-inf">
                        <p class="playcount">{{video.vplaycount}}次播放</p>
                        <p class="uptime">{{video.vupdate}}</p>
                    </div>
                </div>
                <div class="video-content">
                    <video :src="videourl" controls="controls" preload="none" :poster="videoposter"></video>
                    <div class="icon-bar">
                        <i :class="video.agreeicon?beforeagree:agree" @click="clickvideoagree"></i><p>{{video.agree}}</p>
                        <i :class="video.collectionicon?beforecollection:collection" @click="clickvideocollection"></i><p>{{video.collection}}</p>
                        <i :class="other" @click="clickvideoother"></i><p>{{video.other}}</p>
                    </div>
                    <div class="mobie-nav-bar">
                        <ul>
                            <router-link to="/Video/Information" id="mobie-inf">
                                <li>简介</li>
                            </router-link>
                            <router-link to="/Video/Comment">
                                <li>
                                    评论
                                    <p>{{commentlist.length}}</p>
                                </li>
                            </router-link>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="video-inf">
                <p class="introduce-tag">简介</p>
                <pre class="introduce">{{videoinf}}</pre>
                <div class="videotag">
                    <p v-for="(taglist,index) in videotaglist" :key="index">{{taglist.tag}}</p>
                </div>
            </div>
            <div class="video-comment">
                <p class="comment-count">{{commentlist.length}}条评论</p>
                <div class="user-comment">
                    <img class="user-img" :src="touxiang">
                    <textarea id="user-input" class="user-input" v-model="updatecomment.content" :style="{'outline-color':maincolor}"></textarea>
                    <button class="user-inputbtn" @click="addcomment" :style="{'background-color':maincolor}"><p>发表</p><br><p>评论</p></button>
                </div>
                <div class="user-comment-bar" v-for="(comment,index1) in commentlist" :key="index1">
                    <img class="user-img" :src="comment.img">
                    <div class="comment-all">
                        <p class="name">{{comment.name}}</p>
                        <pre class="content">{{comment.content}}</pre>
                        <div class="icon-bar">
                            <p class="comment-date">{{comment.date}}</p>
                            <p class="comment-time">{{comment.time}}</p>
                            <i class="agree" :class="comment.agreeicon?beforeagree:agree" @click="clickcomagree(comment.id,index1)"></i><p>{{comment.agree}}</p>
                            <i class="comment" :class="iconcomment" @click="showcombar(comment.id,index1),getincom(comment.comment,comment.id,index1)"></i><p>{{comment.comment}}</p>
                        </div>
                        <div class="in-comment" v-if="comment.showcombar">
                            <div class="in-comment-box" v-for="(list,index) in comment.incomlist.view" :key="index">
                                <div class="in-user-box">
                                    <img :src="list.img" class="in-user-img">
                                    <p class="name">{{list.name}}</p>
                                </div>
                                <div class="in-comment-content">
                                    <pre class="in-content">{{list.content}}</pre>
                                    <div class="icon-bar">
                                        <p class="comment-date">{{list.date}}</p>
                                        <p class="comment-time">{{list.time}}</p>
                                        <i class="agree" :class="list.agreeicon?beforeagree:agree" @click="clickinagree(list.id,index,index1)"></i><p>{{list.agree}}</p>
                                        <p class="in-comment-p" @click="atrecomd(list.name)" :style="{color:maincolor}">回复</p>
                                    </div>
                                </div>
                            </div>
                            <div class="page-box">
                                <ul>
                                    <li>共{{comment.incomlist.totalPage}}页</li>
                                    <li v-for="(num,index) in comment.incomlist.totalPage" :key="index" :class="{'indexcolor':comment.incomlist.indexstyle == index}" @click="Indexchangestyle(comment.comment,comment.id,index1,index)">{{num}}</li>
                                    <li @click="Nextchangestyle(comment.comment,comment.id,index1)">下一页</li>
                                </ul>
                            </div>
                            <div class="in-user-comment">
                                <textarea id="in-user-input" class="in-user-input" v-model="inupdatecomment.content" :style="{'outline-color':maincolor}"></textarea>
                                <button class="in-user-inputbtn" @click="addincomment(comment.id,index1)" :style="{'background-color':maincolor}"><p>发表</p><br><p>评论</p></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="tip-nonull" v-if="tipshow">
                <p>回复的内容不能一个字也没有哦</p>
            </div>
            <div class="mobie-box">
                <router-view></router-view>
            </div>
        </div>
        <div class="right-box">
            <div class="up-box">
                <div class="up-top-box">
                    <img class="up-img" :src="videoupimg">
                    <div class="up-inf">
                        <p class="up-name">{{videoupname}}</p>
                        <p class="up-speak">{{video.upspeak}}</p>
                    </div>
                </div>
                <div class="up-bottom-box">
                    <button :style="{'background-color':maincolor}">关注</button>
                </div>
            </div>
            <div class="video-list">
                <p class="title">视频选集</p>
                <div class="list-box">
                    <div class="list-li" @click="clickliststyle(index)" :class="{backcolor:liststyle==index}" v-for="(list,index) in videotitlelist" :key="index">
                        <p class="titlename">{{list.title}}</p>
                        <p class="timelong">{{list.length}}</p>
                    </div>
                </div>
            </div>
            <div class="recommend-video">
                <p class="reput">相关推荐</p>
                <div class="recommend-box" v-for="(vlist,index) in revideolist" :key="index">
                    <router-link :to="'/Video/'+vlist.vid" @click.native="setTitle([vlist.title,vlist.name,vlist.upimg])">
                        <img class="cover" :src="vlist.img">
                    </router-link>
                    <div class="right-recommend-box">
                        <p class="title">{{vlist.title}}</p>
                        <p class="name">{{vlist.name}}</p>
                        <p class="playcount">{{vlist.playcount}}万次播放</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name:'Videoplayer',
    data () {
        return {
            touxiang:this.$store.state.userlogin.tximg,
            videourl:require('../static/videos/樱桃小丸子op.mp4'),
            videoposter:require('../assets/png/videoposter.png'),
            videoinf:'在1990年至1992年，《樱桃小丸子》第一次动画化，于富士电视台播出，并曾于1990年10月28日达到39.9%的收视率，在1991年9月26日成为有纪录以来日本史上最高收视的动画。而第二次动画化于1995年开始，直至2005年现仍播放中，并于2005年1月9日迎来了播放15周年纪念。',
            agree:'iconfont icon-dianzan',
            beforeagree:'iconfont icon-dianzan1',
            collection:'iconfont icon-shoucang',
            beforecollection:'iconfont icon-shoucangtianchong',
            other:'iconfont icon-fenxiang',
            iconcomment:'iconfont icon-_ico_reply',
            liststyle:'0',
            indexstyle:'0',
            tipshow:false,
            video:{

            },
            commentlist:[

            ],
            revideolist:[

            ],
            updatecomment:{
                id:'1',
                img:this.$store.state.userlogin.tximg,
                name:this.$store.state.user.name,
                content:'',
                date:'',
                time:'',
                agree:'0',
                comment:'0',
                agreeicon:false,
                commenticon:false,
                showcombar:false,
                incomlist:{
                    pageSize: 5,
                    currentPage: 0,
                    totalPage: 0,
                    indexstyle: 0,
                    view: [

                    ],
                    cominf: [
                       
                    ]
                }
            },
            inupdatecomment:{
                img: this.$store.state.userlogin.tximg,
                name: this.$store.state.user.name,
                content: '',
                date: '',
                time: '',
                agree: 0,
                agreeicon: false,
                id: 1
            },
            videotitlelist:[
                {
                    title:"p1 樱桃小丸子op",
                    length:"1:30"
                },
                {
                    title:"p2 樱桃小丸子op",
                    length:"1:30"
                },
                {
                    title:"p3 樱桃小丸子op",
                    length:"1:30"
                },
                {
                    title:"p4 樱桃小丸子op",
                    length:"1:30"
                },
                {
                    title:"p5 樱桃小丸子op",
                    length:"1:30"
                }
            ],
            videotaglist:[
                {
                    tag:"樱桃小丸子"
                },
                {
                    tag:"音乐"
                },
                {
                    tag:"童年"
                },
                {
                    tag:"日语"
                },
                {
                    tag:"美食"
                },
                {
                    tag:"经典"
                }
            ]
        }
    },
    created () {
        this.getvideo();
        this.getcomment();
        this.getrevideo();
    },
    mounted () {
        this.mobieCilck();
    },
    computed: {
        ...mapState(["videotitle","videoupname","videoupimg","maincolor","userlogin","user"])
    },
    methods: {
        addcomment(){
            let input = document.getElementById("user-input").value;
            let str = input.replace(/(^\s*)|(\s*$)/g, "");
            if(str==''||str==null){
                this.tipshow = true;
                setTimeout(() => {
                    this.tipshow = false;
                }, 1000);
            }else{
                let date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth()+1;
                let showmonth = month<10?'0'+month:month;
                let day = date.getDate();
                let showday = day<10?'0'+day:day;
                let hour = date.getHours();
                let min = date.getMinutes();
                let showmin = min<10?'0'+min:min;
                let adddate = year+'-'+showmonth+'-'+showday;
                let addtime = hour+':'+showmin;
                this.updatecomment.date = adddate;
                this.updatecomment.time = addtime;
                let addcomment = Object.assign({}, this.updatecomment);
                this.commentlist.unshift(addcomment);
                this.updatecomment.content = '';
            }
        },
        addincomment(id,index){
            if(this.commentlist[index].id == id){
                let input = document.getElementById("in-user-input").value;
                let str = input.replace(/(^\s*)|(\s*$)/g, "");
                if(str==''||str==null){
                    this.tipshow = true;
                    setTimeout(() => {
                        this.tipshow = false;
                    }, 1000);
                }else{
                    let date = new Date();
                    let year = date.getFullYear();
                    let month = date.getMonth()+1;
                    let showmonth = month<10?'0'+month:month;
                    let day = date.getDate();
                    let showday = day<10?'0'+day:day;
                    let hour = date.getHours();
                    let min = date.getMinutes();
                    let showmin = min<10?'0'+min:min;
                    let adddate = year+'-'+showmonth+'-'+showday;
                    let addtime = hour+':'+showmin;
                    this.inupdatecomment.date = adddate;
                    this.inupdatecomment.time = addtime;
                    let addcomment = Object.assign({}, this.inupdatecomment);
                    this.commentlist[index].incomlist.cominf.unshift(addcomment);
                    this.inupdatecomment.content = '';
                    this.commentlist[index].comment ++;
                    let pageNum = this.commentlist[index].comment;
                    this.commentlist[index].incomlist.totalPage = Math.ceil(pageNum/this.commentlist[index].incomlist.pageSize);
                    this.commentlist[index].incomlist.cominf.length = pageNum;
                    let page = this.commentlist[index].incomlist.currentPage;
                    let start = this.commentlist[index].incomlist.pageSize * page;
                    let end = this.commentlist[index].incomlist.pageSize * (page+1);
                    this.commentlist[index].incomlist.view = this.commentlist[index].incomlist.cominf.slice(start,end);
                }
            }
        },
        atrecomd(name){
            document.getElementById("in-user-input").value = '@'+name+" : ";
            document.getElementById("in-user-input").focus();
        },
        getvideo(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/video").then((res)=>{
                this.video = res.data.videoinf;
            })
        },
        getcomment(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/videocomment").then((res)=>{
                this.commentlist = res.data.commentlist;
            })
        },
        getrevideo(){
            this.$https.get("http://rap2api.taobao.org/app/mock/288525/revideo").then((res)=>{
                this.revideolist = res.data.revideolist;
            })
        },
        getincom(pageNum,id,index){
            if(this.commentlist[index].id == id){
                this.$https.get("http://rap2api.taobao.org/app/mock/288525/incomment").then((res)=>{
                    this.commentlist[index].incomlist = res.data.comlist;
                    this.commentlist[index].incomlist.totalPage = Math.ceil(pageNum/this.commentlist[index].incomlist.pageSize);
                    this.commentlist[index].incomlist.cominf.length = pageNum;
                    let page = this.commentlist[index].incomlist.currentPage;
                    let start = this.commentlist[index].incomlist.pageSize * page;
                    let end = this.commentlist[index].incomlist.pageSize * (page+1);
                    this.commentlist[index].incomlist.view = this.commentlist[index].incomlist.cominf.slice(start,end);
                });
            }
        },
        Indexchangestyle(pageNum,id,index1,index){
            if(this.commentlist[index1].id == id){
                this.commentlist[index1].incomlist.indexstyle = index;
                this.commentlist[index1].incomlist.cominf.length = pageNum;
                let page = index;
                let start = this.commentlist[index1].incomlist.pageSize * page;
                let end = this.commentlist[index1].incomlist.pageSize * (page+1);
                this.commentlist[index1].incomlist.view = this.commentlist[index1].incomlist.cominf.slice(start,end);
            }
        },
        Nextchangestyle(pageNum,id,index1){
            if(this.commentlist[index1].id == id){
                if(this.commentlist[index1].incomlist.indexstyle<(this.commentlist[index1].incomlist.totalPage - 1)){
                    this.commentlist[index1].incomlist.indexstyle++;
                    this.commentlist[index1].incomlist.cominf.length = pageNum;
                    let page = this.commentlist[index1].incomlist.currentPage;
                    if(page+1>this.commentlist[index1].incomlist.totalPage){
                        return false;
                    }else{
                        page = page + 1;
                        this.commentlist[index1].incomlist.currentPage = page;
                    }
                    let start = this.commentlist[index1].incomlist.pageSize * page;
                    let end = this.commentlist[index1].incomlist.pageSize * (page+1);
                    this.commentlist[index1].incomlist.view = this.commentlist[index1].incomlist.cominf.slice(start,end);
                }
            }
        },
        clickvideoagree(){
            this.video.agreeicon = !this.video.agreeicon;
            if(this.video.agreeicon == true){
                this.video.agree++;
            }
            else{
                this.video.agree--;
            }
        },
        clickvideocollection(){
            this.video.collectionicon = !this.video.collectionicon;
            if(this.video.collectionicon == true){
                this.video.collection++;
            }
            else{
                this.video.collection--;
            }
        },
        clickvideoother(){
            if(this.video.othericon == false){
                this.video.other++;
                this.video.othericon = true;
            }
        },
        clickcomagree(id,index){
            if(this.commentlist[index].id == id){
                this.commentlist[index].agreeicon = !this.commentlist[index].agreeicon;
                if(this.commentlist[index].agreeicon == true){
                    this.commentlist[index].agree++;
                }
                else{
                    this.commentlist[index].agree--;
                }
            }
        },
        clickinagree(id,index,index1){
            if(this.commentlist[index1].incomlist.view[index].id == id){
                this.commentlist[index1].incomlist.view[index].agreeicon = !this.commentlist[index1].incomlist.view[index].agreeicon;
                if(this.commentlist[index1].incomlist.view[index].agreeicon == true){
                    this.commentlist[index1].incomlist.view[index].agree++;
                }
                else{
                    this.commentlist[index1].incomlist.view[index].agree--;
                }
            }
        },
        clickliststyle(index){
            this.liststyle = index;
        },
        setTitle([title,name,img]){
            this.$store.commit('setTitle', [title,name,img]);
            this.getvideo();
            this.getcomment();
            this.getrevideo();
            this.updatecomment.content = '';
            document.documentElement.scrollTop = 0;
        },
        showcombar(id,index){
            if(this.commentlist[index].id == id){
                this.commentlist[index].showcombar = !this.commentlist[index].showcombar;
            }
        },
        mobieCilck(){
            let cilck = document.getElementById('mobie-inf');
            cilck.click();
        }
    }
}
</script>

<style scoped>
@import '../static/css/Videoplayer.css';
</style>