<template>
    <div class="top-box" @click="Backtop" v-show="Topboxshow" :style="{color:maincolor,'border-color':maincolor,'background-color':maincolor+'4b'}">
        TOP
    </div>
</template>

<script>
import {mapState} from "vuex"
export default {
    name:'Backcomeup',
    data () {
        return {
            Topboxshow:false,
        }
    },
    mounted() {
       window.addEventListener("scroll",this.Scroll,true);
    },
    computed: {
        ...mapState(["maincolor"])
    },
    methods: {
        Scroll(){
            let top = document.documentElement.scrollTop;
            top>=500?(this.Topboxshow = true) : (this.Topboxshow = false);
        },
        Backtop(){
            let timer = setInterval(function(){
                let top = document.documentElement.scrollTop;
                let speed = Math.floor(-top/5);
                document.documentElement.scrollTop = top + speed;
                if(top === 0){
                    clearInterval(timer);
                }
            },10)
        }
    }
}
</script>

<style scoped>
@import '../static/css/Backcomeup.css';
</style>