import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import './assets/icon/iconfont.css'
import './assets/icon/iconfont2.css'
import './assets/icon/iconfont3.css'
import './assets/icon/iconfont4.css'
import './assets/icon/iconfont5.css'
import './assets/icon/iconfont6.css'
import './assets/icon/iconfont7.css'
import './assets/icon/iconfont8.css'
import './assets/font/font.css'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

Vue.use(VueAwesomeSwiper)

Vue.config.productionTip = false
Vue.prototype.$https = axios

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')