import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Group from '../views/Group.vue'
import Video from '../views/Video.vue'
import Speakhmoe from '../components/Speakhome.vue'
import Care from '../components/Care.vue'
import Videoplayer from '../components/Videoplayer.vue'
import detailSpeak from '../components/detailSpeak.vue'
import Morevideo from '../views/MoreVideo.vue'
import Moreself from '../components/Moreself.vue'
import Morehot from '../components/Morehot.vue'
import Summary from '../views/Summary.vue'
import sumDynamic from '../components/sumDynamic.vue'
import sumCare from '../components/sumCare.vue'
import sumCollection from '../components/sumCollection.vue'
import sumCollPost from '../components/sumCollPost.vue'
import sumCollVideo from '../components/sumCollVideo.vue'
import sumAnswer from '../components/sumAnswer.vue'
import sumPut from '../components/sumPut.vue'
import sumArticle from '../components/sumArticle.vue'
import sumIdea from '../components/sumIdea.vue'
import sumVideo from '../components/sumVideo.vue'
import Login from '../views/Login.vue'
import tipwp from '../components/tipwp.vue'
import Search from '../views/Search.vue'
import Article from '../views/Article.vue'
import Think from '../views/Think.vue'
import Put from '../views/Put.vue'
import AddIdea from '../views/AddIdea.vue'
import AddArticle from '../views/AddArticle.vue'
import PutVideo from '../views/PutVideo.vue'
import VideoManager from '../views/VideoManager.vue'
import Setting from '../views/Setting.vue'
import MobieVideoInf from '../components/MobieVideoInf.vue'
import MobieVideoCmt from '../components/MobieVideoCmt.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'',
    redirect: '/Home/Main'
  },
  {
    path:'/Home',
    name:'Home',
    component:Home,
    children:[
      {
        path:'Main',
        component:Speakhmoe
      },
      {
        path:'Care',
        component:Care
      }
    ]
  },
  {
    path:'/Group',
    name:'Group',
    component:Group,
    children:[
      {
        path:'Main',
        component:Speakhmoe
      },
      {
        path:'Care',
        component:Care
      }
    ]
  },
  {
    path:'/Video',
    name:'Video',
    component:Video
  },
  {
    path:'/Summary',
    name:'Summary',
    component:Summary,
    children:[
      {
        path:'Dynamic',
        component:sumDynamic
      },
      {
        path:'Care',
        component:sumCare
      },
      {
        path:'Collection',
        component:sumCollection,
        children:[
          {
            path:'Post',
            component:sumCollPost
          },
          {
            path:'Video',
            component:sumCollVideo
          }
        ]
      },
      {
        path:'Answer',
        component:sumAnswer
      },
      {
        path:'Put',
        component:sumPut
      },
      {
        path:'Article',
        component:sumArticle
      },
      {
        path:'Idea',
        component:sumIdea
      },
      {
        path:'Video',
        component:sumVideo
      },
    ]
  },
  {
    path:'/Video/:videoid',
    name:'Videoplayer',
    component:Videoplayer,
    children:[
      {
        path:'/Video/Information',
        component:MobieVideoInf
      },
      {
        path:'/Video/Comment',
        component:MobieVideoCmt
      }
    ]
  },
  {
    path:'/Group/:postid',
    name:'detailSpeak',
    component:detailSpeak
  },
  {
    path:'/Group/Article/:articleid',
    name:'Article',
    component:Article
  },
  {
    path:'/Think/:ideaid',
    name:'Think',
    component:Think
  },
  {
    path:'/Morevideo',
    name:'Morevideo',
    component:Morevideo,
    children:[
      {
        path:'Self',
        component:Moreself
      },
      {
        path:'Hot',
        component:Morehot
      }
    ]
  },
  {
    path:'/Login',
    name:'Login',
    component:Login
  },
  {
    path:'/Login/tip',
    name:'tipwp',
    component:tipwp
  },
  {
    path:'/Search',
    name:'Search',
    component:Search
  },
  {
    path:'/Put',
    name:'Put',
    component:Put
  },
  {
    path:'/AddIdea',
    name:'AddIdea',
    component:AddIdea
  },
  {
    path:'/AddArticle',
    name:'AddArticle',
    component:AddArticle
  },
  {
    path:'/PutVideo',
    name:'PutVideo',
    component:PutVideo
  },
  {
    path:'/VideoManager',
    name:'VideoManager',
    component:VideoManager
  },
  {
    path:'/Setting',
    name:'Setting',
    component:Setting
  },
]

const router = new VueRouter({
  routes,
  mode:'hash'
})

export default router
